/* @jsxImportSource @emotion/react */
import {Component, createRef} from 'react'
import PropTypes from 'prop-types'
import axios from 'axios'
import _get from 'dlv'

import blurPause from 'playerCore/blurPause'
import config from 'config'
import {PlayerViewMode, StorageKey} from 'Enum'
import {isDesktop} from 'util/environment'
import {catchBy} from 'util/index'
import {layer} from 'style'
import {rewriteHlsQualities} from 'playerCore/adaptation'
import PlayerAction, {type} from './store/action/Player'
import UIAction, {type as UIType} from './store/action/UI'
import {type as ApiType} from './store/action/API'
import {autoUnlockAutoplay, preventLeaveFullscreenPause} from 'player/safari'
import {getDefaultDrmConfig, getWidevineLevel} from 'playerCore/drm'

const ISSUER = 'kks-adapter'

const getMaxQuality = (state, {manifestType = 'hls', quality = ''}) => {
  const {
    UI: {selectedQualityName, qualityItems, qualityPrecedence},
    API: {manifests},
  } = state
  const {resolutions = []} = manifests[manifestType]
  const allQualities = quality?.getQualityOptions?.(resolutions) || qualityItems
  const selected =
    qualityPrecedence === 'application'
      ? {height: quality.default || quality}
      : allQualities.find(item => item.height === selectedQualityName) ||
        qualityItems.find(item => item.value === selectedQualityName) ||
        qualityItems[0]

  return (
    allQualities.find(
      item =>
        item.height === selected.height ||
        item.height === parseInt(selected.height, 10)
    ) ||
    resolutions[0] ||
    {}
  )
}

const getLoadOptions = (
  state,
  {startTime, widevine: {policy, mostRobustLevel}, drm}
) => {
  const {
    manifests,
    drm: drmHost,
    playbackToken,
    customHeader: headers,
  } = state.API
  const getDrmConfig = drm ? drm : getDefaultDrmConfig
  return {
    dash: manifests.dash.url,
    hls: manifests.hls.url,
    drm: getDrmConfig({
      host: drmHost,
      token: playbackToken.token,
      headers,
      widevine: {
        policy,
        level: mostRobustLevel,
        blockedDevices: state.API.playbackToken.widevine_blacklist,
      },
    }),
    options: {
      startTime: startTime || _get(state, 'API.playback.updateTime') || 0,
    },
  }
}

const applyLoadPlugins = (plugins = [], manifest, options) =>
  plugins.reduce(async (last, plugin) => {
    const result = await last
    return {
      ...result,
      ...(await plugin.load?.(result, options)),
    }
  }, Promise.resolve(manifest))

const handleSeek = (player, plugins, position) => {
  const seekHandler = plugins.find(
    plugin => typeof plugin.handleSeek === 'function'
  )
  if (seekHandler) {
    seekHandler.handleSeek(position, realPosition =>
      player.seek(realPosition, ISSUER)
    )
    return true
  }
}

const getPlaybackStatus = (player, plugins) =>
  Object.assign(
    {
      currentTime: player.getCurrentTime(),
      duration: player.getDuration(),
    },
    ...plugins.map(plugin => plugin.getPlaybackStatus?.())
  )

const loadStream = async (player, store, props, self) => {
  const state = store.getState()
  const preferredManifestFormat = player.getSupportedTech()[0].streaming
  const baseOptions = getLoadOptions(state, {
    ...(state.API.contentChangedTimes <= 1 && {startTime: props.startTime}),
    widevine: {
      policy: props.widevine,
      mostRobustLevel: await getWidevineLevel(),
    },
    drm: props.drm,
  })
  const {
    url: manifestUrl,
    startTime = baseOptions.startTime,
    ...streamInfo
  } = await applyLoadPlugins(
    props.plugins,
    {
      ...state.API.manifests[preferredManifestFormat],
      url: baseOptions[preferredManifestFormat],
      startTime: baseOptions.options.startTime,
    },
    {
      player,
      video: props.videoRef.current,
      adContainer: props.adContainerRef.current,
    }
  )
  self.streamInfo = streamInfo
  let loadOptions = {
    ...baseOptions,
    [preferredManifestFormat]: manifestUrl,
    options: {startTime},
  }

  if (preferredManifestFormat === 'hls') {
    const {data: manifest} = await axios.get(manifestUrl).catch(() => {
      store.dispatch(PlayerAction.error())
    })
    const getFilteredManifest = () => {
      const maxQuality = getMaxQuality(store.getState(), {
        quality: props.quality,
      })
      const filtered = rewriteHlsQualities(manifest, {
        sourceUrl: manifestUrl,
        filter: qualities =>
          props.onVideoQualityChanged({
            targetQuality: maxQuality,
            qualities,
          }),
      })
      return filtered
        ? URL.createObjectURL(
            new Blob([filtered], {type: 'application/x-mpegURL'})
          )
        : manifestUrl
    }
    self.changeQuality = async () => {
      const startTime = player.getCurrentTime()
      await player.unload()
      return player.load({
        ...loadOptions,
        hls: getFilteredManifest(),
        options: {startTime},
      })
    }
    loadOptions = {
      ...loadOptions,
      hls: getFilteredManifest(),
    }
  }

  return player.load(loadOptions)
}

const handleSafariStalledTooLong = (player, store, PlayerAction) => {
  if (player.getStreamType() !== 'hls') {
    return
  }

  setTimeout(async () => {
    if (player.isStalled()) {
      const {url} = store.getState().API.manifests.hls
      await axios.get(url, {timeout: 5000}).catch(() => {
        store.dispatch(PlayerAction.error())
      })
    }
  }, 10000)
}

const handleSelectQuality = ({
  player,
  targetQuality,
  onVideoQualityChanged,
}) => {
  const availableQualities = player.getAvailableVideoQualities()
  const selected =
    onVideoQualityChanged?.({qualities: availableQualities, targetQuality}) ||
    availableQualities
  return availableQualities.length <= 1 ? availableQualities : selected
}

class BitmovinPlayer extends Component {
  static propTypes = {
    licenseKey: PropTypes.string.isRequired,
    config: PropTypes.object,
    autoPlay: PropTypes.bool,
    quality: PropTypes.number,
    containerRef: PropTypes.object,
    store: PropTypes.object.isRequired,
    plugins: PropTypes.array,
    videoRef: PropTypes.object,

    dispatchSessionEvent: PropTypes.func,
    setPlayer: PropTypes.func,
    onSourceLoaded: PropTypes.func,
    onReady: PropTypes.func,
    onPlay: PropTypes.func,
    onPlaying: PropTypes.func,
    onPaused: PropTypes.func,
    onEnded: PropTypes.func,
    onError: PropTypes.func,
    onTimeChanged: PropTypes.func,
    onSeek: PropTypes.func,
    onSeeked: PropTypes.func,
    onVolumeChanged: PropTypes.func,
    onMuted: PropTypes.func,
    onUnmuted: PropTypes.func,
    onViewModeChange: PropTypes.func,
    onEnterFullscreen: PropTypes.func,
    onExitFullscreen: PropTypes.func,
    onVideoQualityChanged: PropTypes.func,
    onStallStarted: PropTypes.func,
    onStallEnded: PropTypes.func,
  }

  static defaultProps = {
    config: config.BitmovinConfig,
    autoPlay: false,

    onSourceLoaded: () => {},
    onReady: () => {},
    onPlay: () => {},
    onPlaying: () => {},
    onPaused: () => {},
    onEnded: () => {},
    onError: () => {},
    onTimeChanged: () => {},
    onSeek: () => {},
    onSeeked: () => {},
    onVolumeChanged: () => {},
    onMuted: () => {},
    onUnmuted: () => {},
    onViewModeChange: () => {},
    onEnterFullscreen: () => {},
    onExitFullscreen: () => {},
    onStallStarted: () => {},
    onStallEnded: () => {},
  }

  player = null
  preferredStreaming = null
  playingQuality = null
  videoRef = createRef()

  removeListener = () => {}

  componentDidMount() {
    autoUnlockAutoplay()
    this.setUserPause = preventLeaveFullscreenPause()
    this.removeListener = this.props.store.addDispatchedListener(
      async (store, action) => {
        try {
          const container = this.props.containerRef.current
          switch (action.type) {
            case type.MOUNT:
              return await this.mount().then(() =>
                store.dispatch(PlayerAction.mounted())
              )
            case type.FETCH_STATE:
              return store.dispatch(
                PlayerAction.setState(this.convertToPlayerState(this.player))
              )
            case type.LOAD:
              return loadStream(this.player, store, this.props, this)
            case ApiType.REFRESH_PLAYBACK_TOKEN:
              this.player.unload()
              await loadStream(this.player, store, this.props, this)
              this.player.pause()
              return
            case type.UNLOAD:
              this.changeQuality = undefined
              this.props.plugins.forEach(plugin => plugin.reset?.())
              return (
                this.player &&
                this.player
                  .unload()
                  .then(() => store.dispatch(PlayerAction.unloaded()))
              )
            case type.PLAY:
              //play might fail if autoplay is still gated, source is still loading
              //In Safari, some failed play calls does not pops immediately, rejection will happen
              //while garbage collection of <video>, like changing video
              //TODO: check if this still necessary after playback flow is refined
              return this.player
                .play(ISSUER)
                .catch(e => console.log('videoElement.play() failed', e))
            case type.PAUSE:
              this.setUserPause?.()
              return this.player.pause(ISSUER)
            case type.SEEK:
              //can't seek when video is ended, but doing this will trigger a reload, so no need to check
              return (
                handleSeek(this.player, this.props.plugins, action.time) ||
                this.player.seek(Math.max(0, action.time), ISSUER)
              )
            case type.SET_VOLUME:
              this.player.setVolume(action.volume)
              if (action.volume > 0 && this.player.isMuted()) {
                this.player.unmute()
              }
              return
            case type.MUTE:
              return this.player.mute()
            case type.UNMUTE:
              return this.player.unmute()
            case type.ENTER_FULL_SCREEN:
              if (container.requestFullscreen)
                container.requestFullscreen({navigationUI: 'hide'})
              return this.player.setViewMode(PlayerViewMode.FULLSCREEN, {
                fullscreenElement: container,
              })
            case type.LEAVE_FULL_SCREEN:
              return this.player.setViewMode(PlayerViewMode.INLINE, {
                fullscreenElement: container,
              })
            case UIType.SELECT_QUALITY:
              if (!this.player.hasEnded()) {
                this.changeQuality?.()
              }
              this.selectedQualities = handleSelectQuality({
                player: this.player,
                targetQuality: getMaxQuality(store.getState(), {
                  quality: this.props.quality,
                }),
                onVideoQualityChanged: this.props.onVideoQualityChanged,
              })
              return
          }
        } catch (error) {
          // wait for bitmovin error event to fire first
          await new Promise(resolve => setTimeout(resolve, 50))
          store.dispatch(PlayerAction.error(error))
        }
      }
    )
  }

  componentWillUnmount() {
    this.removeBlurListener?.()
    this.removeListener()
    const store = this.props.store
    store.dispatch(PlayerAction.unmount())

    try {
      this.player.destroy()
    } catch (error) {
      store.dispatch(PlayerAction.error(error))
    }
  }

  mount() {
    return import('bitmovin-player').then(
      ({default: {Player, PlayerEvent}}) => {
        const store = this.props.store
        const config = {
          key: this.props.licenseKey,
          ...this.props.config,
          playback: {
            autoplay: this.props.autoPlay,
            ...store.getState().LocalStorage[StorageKey.VOLUME],
            ...this.props.config.playback,
          },
          tweaks: {
            stop_download_on_pause: true,
            native_hls_parsing: true,
          },
          adaptation: {
            onVideoAdaptation: data => {
              if (
                !this.selectedQualities ||
                this.selectedQualities.length < 1 ||
                this.selectedQualities.some(
                  quality => quality.id === data.suggested
                )
              ) {
                return data
              }
              const targetBitrate = this.player
                .getAvailableVideoQualities()
                .find(quality => quality.id === data.suggested)
              return (
                this.selectedQualities.reduce((a, b) => {
                  Math.abs(a.bitrate - targetBitrate) <=
                  Math.abs(b.bitrate - targetBitrate)
                    ? a
                    : b
                }).id || data
              )
            },
          },
        }
        const player = new Player(this.videoRef.current, config)
        if (!isDesktop()) {
          this.removeBlurListener = blurPause({player})
        }
        this.props.setPlayer(player)
        player.setVideoElement(this.props.videoRef.current)
        this.preferredStreaming = player.getSupportedTech()[0].streaming
        const withErrorHandling = catchBy(error =>
          store.dispatch(PlayerAction.error(error))
        )
        player.on(
          PlayerEvent.SourceLoaded,
          withErrorHandling(() => {
            player
              .getVideoElement()
              .setAttribute('disablePictureInPicture', true)
            this.workaround_ST_3009({PlayerEvent, player})
            this.workaround_ST_4489({PlayerEvent, player})
            this.selectedQualities = handleSelectQuality({
              player: this.player,
              targetQuality: getMaxQuality(store.getState(), {
                quality: this.props.quality,
              }),
              onVideoQualityChanged: this.props.onVideoQualityChanged,
            })
            store.dispatch(
              PlayerAction.setState(this.convertToPlayerState(this.player))
            )
            store.dispatch(PlayerAction.loaded())
            this.props.dispatchSessionEvent('playbackBegan', {
              ...store.getState().API,
              ...this.streamInfo,
            })
            if (player.isLive()) store.dispatch(PlayerAction.ready())
            this.props.onSourceLoaded()
          })
        )
        player.on(
          PlayerEvent.Ready,
          withErrorHandling(() => {
            store.dispatch(
              PlayerAction.setState(this.convertToPlayerState(this.player))
            )
            store.dispatch(PlayerAction.ready())
            this.props.onReady()
          })
        )
        player.on(
          PlayerEvent.Play,
          withErrorHandling(() => {
            this.props.onPlay()
          })
        )
        player.on(
          PlayerEvent.Playing,
          withErrorHandling(() => {
            store.dispatch(PlayerAction.playing())
            this.props.onPlaying()
          })
        )
        player.on(
          PlayerEvent.Paused,
          withErrorHandling(() => {
            store.dispatch(PlayerAction.paused())
            this.props.onPaused()
          })
        )
        player.on(
          PlayerEvent.PlaybackFinished,
          withErrorHandling(() => {
            store.dispatch(PlayerAction.ended())
            this.props.onEnded()
          })
        )
        player.on(
          PlayerEvent.TimeChanged,
          withErrorHandling(() => {
            this.workaround_OTP_1783({PlayerEvent, player})
            const {currentTime, adRemainingTime} = getPlaybackStatus(
              player,
              this.props.plugins
            )
            store.dispatch(
              PlayerAction.updateCurrentTime({currentTime, adRemainingTime})
            )
            this.props.onTimeChanged(currentTime)
          })
        )
        let lastSeekEventIssuer
        player.on(
          PlayerEvent.Seek,
          withErrorHandling(({seekTarget, position, issuer}) => {
            this.props.dispatchSessionEvent('userSeeking')
            lastSeekEventIssuer = issuer
            if (issuer === ISSUER || issuer === 'internal') {
              this.props.onSeek(seekTarget, position)
            }
          })
        )
        player.on(
          PlayerEvent.Seeked,
          withErrorHandling(() => {
            if (
              lastSeekEventIssuer === ISSUER ||
              lastSeekEventIssuer === 'internal'
            ) {
              const {currentTime} = getPlaybackStatus(
                player,
                this.props.plugins
              )
              store.dispatch(PlayerAction.seeked(currentTime))
              this.props.onSeeked(currentTime)
            }
          })
        )
        player.on(
          PlayerEvent.VolumeChanged,
          withErrorHandling(({targetVolume, sourceVolume}) => {
            store.dispatch(PlayerAction.updateVolume(targetVolume))
            this.props.onVolumeChanged(targetVolume, sourceVolume)
          })
        )
        player.on(
          PlayerEvent.Muted,
          withErrorHandling(() => {
            store.dispatch(PlayerAction.muted())
            this.props.onMuted()
          })
        )
        player.on(
          PlayerEvent.Unmuted,
          withErrorHandling(() => {
            store.dispatch(PlayerAction.unmuted())
            this.props.onUnmuted()
          })
        )
        player.on(PlayerEvent.ViewModeChanged, viewModeEvent => {
          store.dispatch(PlayerAction.updateViewMode(viewModeEvent.to))
          this.props.onViewModeChange(viewModeEvent)
          if (viewModeEvent.to === 'fullscreen') {
            this.props.onEnterFullscreen()
            player.getContainer().setAttribute('data-fullscreen', false)
          } else if (viewModeEvent.from === 'fullscreen') {
            this.props.onExitFullscreen()
          }
        })
        player.on(PlayerEvent.SegmentRequestFinished, () => {
          const getBufferLength = () =>
            Math.min(
              player.getVideoBufferLength() || 0,
              player.getAudioBufferLength() || 0
            )
          store.dispatch(PlayerAction.updateBufferTime(getBufferLength()))
        })
        player.on(PlayerEvent.StallStarted, () => {
          this.props.onStallStarted()
          store.dispatch(PlayerAction.stalling())
          handleSafariStalledTooLong(player, store, PlayerAction)
        })
        player.on(PlayerEvent.StallEnded, () => {
          this.props.onStallEnded()
          store.dispatch(PlayerAction.stalled())
        })
        player.on(PlayerEvent.CueEnter, event => {
          const {text, start, end} = event
          store.dispatch(UIAction.updateSubtitlesContent({text, start, end}))
        })
        player.on(PlayerEvent.CueExit, () =>
          store.dispatch(UIAction.updateSubtitlesContent({}))
        )
        player.on(PlayerEvent.Error, error =>
          store.dispatch(PlayerAction.error(error))
        )
        this.props.dispatchSessionEvent('playerStarted')
        return (this.player = player)
      }
    )
  }

  convertToPlayerState(player) {
    const {currentTime, duration} = getPlaybackStatus(
      player,
      this.props.plugins
    )
    return {
      viewMode: player.getViewMode(),
      streamType: player.getStreamType(),
      progress: {
        progressTime: currentTime,
        duration: isFinite(duration) && duration > 0 ? duration : 0,
        bufferTime: Math.min(
          player.getVideoBufferLength() || 0,
          player.getAudioBufferLength() || 0
        ),
      },
      volume: {
        volume: player.getVolume(),
        muted: player.isMuted(),
      },
      source: player.getSource(),
    }
  }

  // https://kkvideo.atlassian.net/browse/ST-3009
  workaround_ST_3009({PlayerEvent, player}) {
    if (player.getStreamType() === 'dash') {
      let timerId
      let lastTime
      const startDetect = () => {
        if (player.isPlaying()) {
          stopDetect()
          timerId = setInterval(() => {
            const currentTime = player.getCurrentTime() * 1000
            if (player.isPlaying()) {
              if (currentTime - lastTime === 0) {
                player.pause()
                player.play()
              }
            }
            lastTime = currentTime
          }, 4000)
        }
      }
      const stopDetect = () => {
        clearInterval(timerId)
        lastTime = 0
      }
      player.on(PlayerEvent.Playing, startDetect)
      player.on(PlayerEvent.Seeked, startDetect)
      player.on(PlayerEvent.StallEnded, startDetect)
      player.on(PlayerEvent.Paused, stopDetect)
      player.on(PlayerEvent.Seek, stopDetect)
      player.on(PlayerEvent.StallStarted, stopDetect)
      player.on(PlayerEvent.Error, stopDetect)
      player.on(PlayerEvent.PlaybackFinished, stopDetect)
      player.on(PlayerEvent.Destroy, stopDetect)
    }

    this.workaround_ST_3009 = () => {}
  }

  // https://kkvideo.atlassian.net/browse/ST-4489
  workaround_ST_4489({PlayerEvent, player}) {
    player.on(PlayerEvent.StallEnded, () => {
      const video = player.getVideoElement()
      video.currentTime = player.getCurrentTime()
    })
  }

  workaround_OTP_1783({PlayerEvent, player}) {
    if (player.isLive() && player.getStreamType() === 'dash') {
      player.on(PlayerEvent.Paused, () => {
        const videoElem = this.player.getVideoElement()
        if (!videoElem._play) {
          let fromExternalPlay = false
          videoElem._play = videoElem.play
          videoElem.play = function () {
            if (fromExternalPlay) {
              fromExternalPlay = false
              return videoElem._play()
            } else {
              return Promise.reject()
            }
          }
          player._play = player.play
          player.play = function () {
            fromExternalPlay = true
            return player._play()
          }
        }
      })
      player.on(PlayerEvent.Play, () => {
        const videoElem = this.player.getVideoElement()
        if (videoElem._play) {
          videoElem.play = videoElem._play
          videoElem._play = undefined
        }
        if (player._play) {
          player.play = player._play
          player._play = undefined
        }
      })
    }

    this.workaround_OTP_1783 = () => {}
  }

  render() {
    return (
      <div className="kks-player__video" css={layer} ref={this.videoRef}>
        <video ref={this.props.videoRef} />
      </div>
    )
  }
}

export default BitmovinPlayer
