import {useContext, useEffect} from 'react'
import _get from 'dlv'

import {Context} from 'player/store/index'
import PlayerAction from 'player/store/action/Player'
import APIAction from 'player/store/action/API'
import {useTimeout} from 'Hooks'
import {APIState} from 'Enum'

const SYNC_INTERVAL = 10000

const useSyncLinearInfo = () => {
  const {
    state: {apiState, content},
    dispatch,
  } = useContext(Context.API)
  const [setTimeout, clearTimer] = useTimeout()

  useEffect(() => {
    if (apiState === APIState.READY && content.contentType === 'lives') {
      if (_get(content, 'end')) {
        dispatch(PlayerAction.pause())
      } else {
        setTimeout(() => dispatch(APIAction.refreshContent()), SYNC_INTERVAL)
        return clearTimer
      }
    }
  }, [content, apiState])
}

export {useSyncLinearInfo}
