/* @jsxImportSource @emotion/react */
import {keyframes} from '@emotion/react'
import PropTypes from 'prop-types'
import {mask} from 'style'

const rotateInfinite = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100%  {
    transform: rotate(360deg); 
  }
`

const style = {
  display: 'block',
  animation: `${rotateInfinite} 1.2s linear infinite`,
  border: '0.25em solid #f22e05',
  borderRightColor: 'transparent',
  borderRadius: '50%',
  height: '3em',
  width: '3em',
}

const Loading = ({css, styles}) => (
  <div css={[mask, css, styles]}>
    <div className="kks-player__loading" css={style} />
  </div>
)
Loading.propTypes = {
  css: PropTypes.object,
  styles: PropTypes.object,
}

export default Loading
