import {useEffect, useContext} from 'react'

import useStore from 'player/store/useStore'
import {Context} from 'player/store/index'
import APIAction from 'player/store/action/API'
import UIAction from 'player/store/action/UI'

const getEndedStatus = ({Player, API}) => {
  if (Player.ended) {
    return 'ended'
  }
  const endStartSeconds =
    API.content?.time?.endStart || Player.process?.duration - 10

  return Player.progress?.progressTime >= endStartSeconds ? 'end-start' : 'none'
}

const getAutoplayNextActive = state => {
  const endStatus = getEndedStatus(state)
  const {API, UI} = state

  return (
    Boolean(API.content?.recommend?.next) &&
    ((endStatus === 'end-start' && UI.streamEvents.length === 0) ||
      endStatus === 'ended')
  )
}

const getCurrentTime = ({Player}) => Player.progress?.progressTime

const useUpdateLastPlayed = () => {
  const playStatus = useStore(({Player}) =>
    Player.seeking ? 'seeking' : Player.isPlaying ? 'playing' : 'paused'
  )
  const progressTime = useStore(state => Math.floor(getCurrentTime(state) / 10))
  const isVod = useStore(({API}) => API.content?.contentType === 'videos')
  const store = useContext(Context.Adapter)

  useEffect(() => {
    if (isVod && playStatus !== 'seeking') {
      store.dispatch(
        APIAction.updateLastPlayedTime(
          getCurrentTime(store.useStore.getState())
        )
      )
    }
  }, [playStatus, progressTime])
}

const useUpdateEnded = () => {
  const endStatus = useStore(getEndedStatus)
  const store = useContext(Context.Adapter)

  useEffect(() => {
    if (endStatus === 'ended') {
      store.dispatch(APIAction.updatePlayedEnd())
    }
  }, [endStatus === 'ended'])
}

const useAutoPlayNext = ({enabled}) => {
  const active = useStore(state => enabled && getAutoplayNextActive(state))
  const store = useContext(Context.Adapter)

  useEffect(() => {
    if (active) {
      store.dispatch(UIAction.offerAutoplay())
    }
  }, [active])
}

const useVodHandlers = ({autoPlayNext}) => {
  useUpdateLastPlayed()
  useUpdateEnded()
  useAutoPlayNext({enabled: autoPlayNext})
}

export {useVodHandlers}
