import {useState, useEffect, useContext, useRef} from 'react'
import {Context} from 'player/store/index'
import createStore from 'player/store/createStore'

import {isDesktop} from 'util/environment'
import PlayerAction from 'player/store/action/Player'
import UIAction from 'player/store/action/UI'

export const useMenu = () => {
  const {dispatch} = useContext(Context.UI)
  const show = () => {
    if (!isDesktop()) {
      dispatch(PlayerAction.pause())
    }
    dispatch(UIAction.showMenu())
  }
  return [show]
}

export const useStore = (initState, reducer, middlewares) => {
  const [store] = useState(() => createStore(initState, reducer, middlewares))
  return store
}

const useVisibilityState = () => {
  const visibility = useRef({})

  useEffect(() => {
    document.addEventListener('visibilitychange', () => {
      visibility.current.lastUpdate = Date.now()
    })
  }, [])

  return {
    isVisibilityChangedRecently: (inSeconds = 10000) =>
      Date.now() - visibility.current.lastUpdate < inSeconds,
  }
}

export {useVisibilityState}
