import {useContext} from 'react'

import {Context} from 'player/store/index'
import PlayerAction from 'player/store/action/Player'

const usePlayerVolume = () => {
  const {
    state: {volume, muted},
    dispatch,
  } = useContext(Context.PlayerVolume)

  return {
    volume,
    muted,
    //TODO: also unmute
    changeVolume: value => dispatch(PlayerAction.setVolume(value)),
    toggleMuted: () =>
      dispatch(muted ? PlayerAction.unmute() : PlayerAction.mute()),
  }
}

export default usePlayerVolume
