import {type as PlayerType} from 'player/store/action/Player'
import {type as UIType} from 'player/store/action/UI'

const logger = store => next => action => {
  const flag =
    action.type !== PlayerType.UPDATE_CURRENT_TIME &&
    action.type !== PlayerType.UPDATE_BUFFER_TIME &&
    action.type !== UIType.SHOW_THUMBNAIL &&
    action.type !== UIType.FOCUS_SEEK_BAR
  // eslint-disable-next-line no-undef
  if (process.env.NODE_ENV === 'development') {
    flag && console.debug(action.type)
    flag && console.debug('dispatching', action)
  }
  let result = next(action)
  // eslint-disable-next-line no-undef
  if (process.env.NODE_ENV === 'development') {
    flag && console.debug('next state', store.getState())
    flag && console.groupEnd()
  }
  return result
}

export default logger
