import {ClassNames} from '@emotion/react'
import {useReducer, useContext, useRef} from 'react'
import PropTypes from 'prop-types'

import SimpleSlider from 'component/SimpleSlider'
import VideoThumbnail from 'component/VideoThumbnail'
import {useOptions} from 'player/playerContext'
import {Context} from 'player/store/index'
import useThumbnail from 'player/store/useThumbnail'
import Action from 'player/store/action/Player'
import operator from 'player/store/operator'
import formattedTime from 'util/formattedTime'
import {havePointerQuery} from 'util/environment'

const seekbarStyle = {
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  width: '100%',
  height: '24px',
  fontSize: '75%',
  letterSpacing: '1px',
  color: '#fff',
}

const sliderStyle = {
  flex: 1,
  margin: '0 1em',
  [`@media ${havePointerQuery}`]: {
    '> div:last-of-type': {
      opacity: 0,
      transition: 'opacity 0.2s ease-out',
    },
  },
  '&:hover > div:last-of-type': {
    opacity: 1,
  },
}

const thumbnailStyle = {
  left: '0',
  bottom: '28px',
}

const getSliderStyle = css => ({
  rail: css({height: '0.33em'}),
  marked: css({height: '0.33em'}),
  thumb: css({width: '1.33em', height: '1.33em'}),
})

const getThumbailOffset = (container, pointerX, {width} = {}) => {
  const leftOffset = container?.offsetLeft || 0
  const rightOffset = leftOffset + container?.offsetWidth - width
  return Math.max(
    -leftOffset,
    Math.min(pointerX - leftOffset - width / 2, rightOffset)
  )
}

const reducePointer = (state, {type, value, x}) => {
  switch (type) {
    case 'move':
      return {...state, hover: true, value, x}
    case 'change':
      return {...state, focused: true, value}
    case 'release':
      return {...state, focused: false, value}
    case 'leave':
      return {...state, hover: false}
  }
}

const Seekbar = ({style, classes}) => {
  const {thumbnailSeeking} = useOptions()
  const {
    state: {progressTime, seekTime, bufferTime, duration},
    dispatch,
  } = useContext(Context.PlayerProgress)
  const {isSettingsOpen, streamEvents = []} = useContext(Context.UI).state
  const [pointerState, dispatchPointer] = useReducer(reducePointer, {})
  const containerRef = useRef()
  const currentTime = seekTime >= 0 ? seekTime : progressTime
  const pointerActive = pointerState.hover || pointerState.focused
  const displayTime = pointerActive ? pointerState.value : currentTime
  const thumbnail = useThumbnail(pointerState.value)
  const displayThumbnail = !isSettingsOpen && thumbnailSeeking && pointerActive

  return (
    <div
      className="kks-player__seek-bar"
      css={[seekbarStyle, style]}
      ref={containerRef}
    >
      {formattedTime(displayTime)}
      <ClassNames>
        {({css}) => (
          <SimpleSlider
            css={sliderStyle}
            classes={{
              track: css({backgroundColor: 'red'}),
              ...getSliderStyle(css),
              ...classes,
            }}
            secondaryTrackValue={progressTime + bufferTime}
            value={currentTime}
            max={duration}
            marks={streamEvents.map(event => event.start)}
            onPointerMove={(_, {value, x}) =>
              dispatchPointer({type: 'move', value, x})
            }
            onPointerLeave={() => dispatchPointer({type: 'leave'})}
            onChange={(_, {value}) => {
              dispatch(Action.pause())
              dispatchPointer({type: 'change', value})
            }}
            //play will be triggered in operator.seek
            onChangeCommitted={(_, {value}) => {
              dispatchPointer({type: 'release', value})
              dispatch(operator.seek(value))
            }}
          />
        )}
      </ClassNames>
      {formattedTime(duration)}
      {displayThumbnail && (
        <VideoThumbnail
          css={thumbnailStyle}
          style={{
            transform: `translateX(${getThumbailOffset(
              containerRef.current,
              pointerState.x,
              thumbnail?.position
            )}px)`,
          }}
          time={pointerState.value}
          image={thumbnail.image}
          {...thumbnail.position}
        />
      )}
    </div>
  )
}

Seekbar.propTypes = {
  style: PropTypes.object,
  classes: PropTypes.object,
  thumbnailSeeking: PropTypes.bool,
}

export default Seekbar
