/* @jsxImportSource @emotion/react */
import {Fragment} from 'react'
import PropTypes from 'prop-types'

const _css = {
  userSelect: 'none',
  color: 'white',
  fontSize: 20,
  whiteSpace: 'nowrap',
  display: 'inline-flex',
  alignItems: 'center',
  part: {
    width: 26,
    display: 'inline-block',
    textAlign: 'center',
  },
}

const FormattedTime = ({time, showHour, style}) => {
  time = Math.floor(time)
  const second = time % 60
  const minute = ((time - second) / 60) % 60
  const hour = (time - second - minute * 60) / 60 / 60
  return (
    <span className="kks-player__formatted-time" css={[_css, style]}>
      {(showHour || hour !== 0) && (
        <Fragment>
          <span
            className="kks-player__formatted-time__part"
            css={[_css.part, style.part]}
          >
            {String(hour).padStart(2, '0')}
          </span>
          :
        </Fragment>
      )}
      <span
        className="kks-player__formatted-time__part"
        css={[_css.part, style.part]}
      >
        {String(minute).padStart(2, '0')}
      </span>
      :
      <span
        className="kks-player__formatted-time__part"
        css={[_css.part, style.part]}
      >
        {String(second).padStart(2, '0')}
      </span>
    </span>
  )
}
FormattedTime.propTypes = {
  time: PropTypes.number,
  showHour: PropTypes.bool,
  style: PropTypes.shape({
    part: PropTypes.object,
  }),
}
FormattedTime.defaultProps = {
  style: {},
}

export default FormattedTime
