import PropTypes from 'prop-types'

import Types from 'Types'
import {CastState} from 'Enum'
import Tooltip from 'player/container/ControlsTooltip'
import Message from 'context/I18n/Message'
import {useCastContext} from 'cast/context'
import CastButton from 'component/Button/CastButton'

const CastVideoButton = ({contentId, contentType, mediaSource, ...props}) => {
  const {
    state: {castState},
    actions: {connect, loadContent, stopCasting},
  } = useCastContext()
  const tooltip =
    castState === CastState.CONNECTED
      ? 'KKS.PLAYER.CAST.DISCONNECT'
      : 'KKS.PLAYER.CAST'
  const handleCast = () => {
    if (castState === CastState.CONNECTED) {
      stopCasting()
    } else if (contentId && contentType) {
      loadContent({contentId, contentType, mediaSource})
    } else {
      connect()
    }
  }

  return (
    <Tooltip title={<Message code={tooltip} />} bottom="48px">
      <CastButton castState={castState} onClick={handleCast} {...props} />
    </Tooltip>
  )
}

CastVideoButton.propTypes = {
  contentId: PropTypes.string,
  contentType: Types.ItemType,
  mediaSource: PropTypes.string,
}

export default CastVideoButton
