import {useContext} from 'react'
import PropTypes from 'prop-types'

import icon from 'style/icon'
import Tooltip from 'player/container/ControlsTooltip'
import Message from 'context/I18n/Message'
import {Context} from 'player/store/index'
import IconButton from './IconButton'
import {useMenu} from 'player/Hooks'

import {getSettingSections} from 'player/settings'

const SettingButton = ({style}) => {
  const {
    state: {content},
  } = useContext(Context.API)
  const {qualityItems, mediaSources} = useContext(Context.UI).state
  const [showMenu] = useMenu()
  const haveSettings =
    getSettingSections({
      contentType: content?.contentType,
      mediaSources,
      qualityItems,
    }).length > 0

  return (
    haveSettings && (
      <Tooltip title={<Message code="KKS.SETTING" />} bottom="48px">
        <IconButton
          style={style}
          name="kks-player__setting-button"
          src={icon.setting}
          onClick={event => showMenu(event)}
        />
      </Tooltip>
    )
  )
}

SettingButton.propTypes = {
  style: PropTypes.object,
}

export default SettingButton
