/* eslint-disable react/jsx-no-target-blank */
/* @jsxImportSource @emotion/react */
import {useContext} from 'react'
import classnames from 'classnames'

import {CastState} from 'Enum'
import {useCastContext, useCastVolume} from 'cast/context'
import FunctionBar from 'component/Panel/FunctionBar'
import Tooltip from 'component/Tooltip'
import VolumeControl from 'component/VolumeControl'
import SkipButton from 'component/Button/SkipButton'
import CastButton from 'player/container/Button/CastButton'
import I18n from 'context/I18n'
import Seekbar from './Seekbar'
import PlayButton from './PlayButton'
import RewindButton from './RewindButton'
import ForwardButton from './ForwardButton'
import PreviousEpisodeButton from './PreEpisodeButton'
import NextVideoButton from './NextVideoButton'

const style = {
  position: 'fixed',
  bottom: '0',
  zIndex: '3',
  width: '100%',
  height: '72px',
  alignItems: 'center',
  backgroundColor: 'rgba(34, 34, 34, 0.75)',
  color: '#fff',
  '> *': {
    flex: '1',
  },
}

const controlStyle = {
  padding: '0 18px 6px',
  '> *': {
    margin: '0 8px',
  },
  '> div:first-child': {
    // seekbar
    margin: '0 0 6px',
  },
}

const messageStyle = {
  flex: '1',
  minWidth: '0',
  textAlign: 'center',
  whiteSpace: 'nowrap',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
}

const adStatusStyle = {
  flex: '1',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  a: {
    display: 'block',
    textDecoration: 'underline',
    '&:visited': {
      color: 'inherit',
    },
  },
  button: {
    margin: '0 1.5rem',
    padding: '0.2rem 0.5rem',
    width: 'auto',
    border: '2px solid rgba(255, 255, 255, 0.7)',
    fontSize: '120%',
  },
}

const castButtonTooltip = {
  [CastState.CONNECTED]: 'KKS.PLAYER.CAST.DISCONNECT',
  [CastState.NOT_CONNECTED]: 'KKS.PLAYER.CAST',
}

const MiniControl = () => {
  const {translate} = useContext(I18n.Context)
  const {
    state: {
      castState,
      deviceName,
      mediaTitle,
      playerState,
      streamType,
      isPlayingBreak,
      currentBreakTime,
      whenSkippable,
      clickThroughUrl,
    },
    actions: {skipAd},
  } = useCastContext()
  const secondsToSkip = Math.ceil(whenSkippable - currentBreakTime)
  const isIdle =
    castState === 'CONNECTED' && (!playerState || playerState === 'IDLE')
  const showIfConnected = {
    display: castState === 'CONNECTED' ? 'flex' : 'none',
  }
  const messageProperties = {
    VIDEO: mediaTitle,
    CHROMECAST: deviceName,
  }
  const connectedMessage = translate('KKS.CAST.CONNTECTED', messageProperties)
  const castingMessage = translate('KKS.CAST.STATUS', messageProperties)

  return (
    <div
      className={classnames('kks-player__minictrl', {
        'kks-player__minictrl--show': castState === 'CONNECTED',
      })}
      css={[showIfConnected, style]}
    >
      {isIdle ? (
        <Tooltip overflowOnly title={connectedMessage}>
          <div className="kks-player__minictrl__cast-name" css={messageStyle}>
            {connectedMessage}
          </div>
        </Tooltip>
      ) : (
        <FunctionBar style={controlStyle}>
          {!isPlayingBreak && <Seekbar />}
          <PreviousEpisodeButton />
          <PlayButton />
          <NextVideoButton />
          {isPlayingBreak ? (
            <div css={adStatusStyle}>
              <a href={clickThroughUrl} rel="noopener" target="_blank">
                <I18n.Message code="KKS.SSAI.LEARN.MORE" wrap={false} />
              </a>
              {whenSkippable > 0 && (
                <SkipButton remainingTime={secondsToSkip} onClick={skipAd} />
              )}
            </div>
          ) : (
            <>
              {streamType !== 'LIVE' && <RewindButton />}
              {streamType !== 'LIVE' && <ForwardButton />}
              <Tooltip overflowOnly title={castingMessage}>
                <div css={messageStyle}>{castingMessage}</div>
              </Tooltip>
            </>
          )}
          <VolumeControl displaySlider useVolumeState={useCastVolume} />
          <Tooltip
            title={translate(castButtonTooltip[castState])}
            bottom="24px"
          >
            <CastButton />
          </Tooltip>
        </FunctionBar>
      )}
    </div>
  )
}

export default MiniControl
