import {useEffect, useState, useRef, createContext, useContext} from 'react'
import PropTypes from 'prop-types'
import Types from 'Types'
import {SeekOrigin} from 'Enum'
import {
  ensureSenderFramework,
  ensureSession,
  getMediaSession,
  requestLoad,
} from './framework'

const castContext = createContext()

const initState = {
  castState: null,
  playerState: null,
  castingMedia: null,
  deviceName: null,
  mediaTitle: null,
  duration: 0,
  progressTime: 0,
  customData: {},
  volume: 100,
  muted: false,
  streamType: null,
}

const defaultCastContext = {
  state: {},
  actions: {
    hasPrevious: () => false,
    hasNext: () => false,
  },
}

/* global chrome, cast */
const CastProvider = ({
  appId,
  host,
  accessToken,
  deviceId,
  customHeader,
  lang,
  onConnected,
  onCasting,
  onError,
  children,
}) => {
  const actions = useRef(defaultCastContext.actions)
  const [state, setState] = useState(initState)
  const config = useRef()
  config.current = {
    appId,
    host,
    accessToken,
    deviceId,
    customHeaders: customHeader,
    lang,
  }

  useEffect(() => {
    const setCastState = value => setState(current => ({...current, ...value}))
    ensureSenderFramework()
      .then(context => {
        context.setOptions({
          receiverApplicationId: appId,
          resumeSavedSession: true,
          autoJoinPolicy: chrome.cast.AutoJoinPolicy.ORIGIN_SCOPED,
        })
        context.addEventListener(
          cast.framework.CastContextEventType.CAST_STATE_CHANGED,
          ({castState}) => {
            const currentSession = context.getCurrentSession()
            const current = {
              castState,
              deviceName:
                castState === 'CONNECTED' &&
                currentSession.getCastDevice().friendlyName,
              ...(currentSession && {
                volume: currentSession.getVolume(),
                muted: currentSession.isMute(),
              }),
            }
            if (castState === 'CONNECTED') {
              if (typeof onConnected === 'function') onConnected(current)
            }

            setCastState(current)
          }
        )

        const player = new cast.framework.RemotePlayer()
        const controller = new cast.framework.RemotePlayerController(player)

        controller.addEventListener(
          cast.framework.RemotePlayerEventType.PLAYER_STATE_CHANGED,
          ({value}) =>
            setCastState({
              playerState: value,
              castingMedia: value !== 'IDLE' ? getMediaSession().media : null,
            })
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.TITLE_CHANGED,
          ({value}) => setCastState({mediaTitle: value})
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.MEDIA_INFO_CHANGED,
          ({value}) =>
            setCastState({
              castingMedia: value,
              // After playing a while, media info is updated without customData
              // It's likely receiver app bug
              ...(value
                ? value.customData && {customData: value.customData}
                : {customData: {}}),
            })
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.DURATION_CHANGED,
          ({value}) => setCastState({duration: value})
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.CURRENT_TIME_CHANGED,
          ({value}) => setCastState({progressTime: value})
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.VOLUME_LEVEL_CHANGED,
          ({value}) => setCastState({volume: value})
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.IS_MUTED_CHANGED,
          ({value}) => setCastState({muted: value})
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.MEDIA_INFO_CHANGED,
          ({value}) => setCastState({streamType: value && value.streamType})
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.IS_PLAYING_BREAK_CHANGED,
          ({value}) => setCastState({isPlayingBreak: value})
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.BREAK_CLIP_ID_CHANGED,
          ({value}) =>
            setCastState({
              breakClipId: value,
              clickThroughUrl: (getMediaSession().media?.breakClips || []).find(
                item => item.id === value
              )?.clickThroughUrl,
            })
        )
        controller.addEventListener(
          cast.framework.RemotePlayerEventType.CURRENT_BREAK_TIME_CHANGED,
          ({value}) => {
            setCastState({
              currentBreakTime: value,
              whenSkippable: player.whenSkippable,
            })
          }
        )

        const getItem = (media, index) => {
          const items = media.items || []
          return items[(items.length + index) % items.length] || {}
        }

        return {
          connect: () => ensureSession(),
          loadContent: ({
            contentId,
            contentType,
            licenseId,
            mediaSource,
            customQuery,
            startTime,
          }) =>
            ensureSession()
              .then(currentSession =>
                requestLoad(currentSession, {
                  id: contentId,
                  mediaInfo: {
                    contentId,
                    contentID: contentId,
                    customData: {
                      ...config.current,
                      itemType: contentType,
                      licenseId,
                      mediaSource,
                      customQuery,
                    },
                  },
                  currentTime: startTime,
                })
              )
              .then(() => {
                typeof onCasting === 'function' && onCasting()
              })
              .catch(error => {
                typeof onError === 'function' && onError(error)
                return Promise.reject(error)
              }),
          stopCasting: () => context.endCurrentSession(true),
          play: () =>
            getMediaSession().play(new chrome.cast.media.PlayRequest()),
          pause: () =>
            getMediaSession().pause(new chrome.cast.media.PauseRequest()),
          seek: ({origin, seconds}) => {
            const media = getMediaSession()
            const currentTime =
              seconds +
              (origin === SeekOrigin.CURRENT ? media.getEstimatedTime() : 0)
            media.seek(
              Object.assign(new chrome.cast.media.SeekRequest(), {currentTime})
            )
          },
          skipAd: () => controller.skipAd(),
          hasPrevious: () => {
            const media = getMediaSession() || {}
            return media.currentItemId !== getItem(media, 0).itemId
          },
          hasNext: () => {
            const media = getMediaSession() || {}
            return media.currentItemId !== getItem(media, -1).itemId
          },
          changePreviousEpisode: () =>
            new Promise((resolve, reject) =>
              getMediaSession().queuePrev(resolve, reject)
            ),
          changeNextEpisode: () =>
            new Promise((resolve, reject) =>
              getMediaSession().queueNext(resolve, reject)
            ),
          setVolume: volume => context.getCurrentSession().setVolume(volume),
          setMute: mute => context.getCurrentSession().setMute(mute),
        }
      })
      .then(result => {
        actions.current = result
      })
      .catch(error => typeof onError === 'function' && onError(error))
  }, [])

  return (
    <castContext.Provider value={{state, actions: actions.current}}>
      {children}
    </castContext.Provider>
  )
}

CastProvider.propTypes = {
  appId: PropTypes.string,
  host: PropTypes.string,
  accessToken: PropTypes.string,
  deviceId: PropTypes.string,
  customHeader: PropTypes.object,
  lang: Types.LanguageCode,
  children: PropTypes.node,
  onConnected: PropTypes.func,
  onCasting: PropTypes.func,
  onError: PropTypes.func,
}

const useCastContext = () => useContext(castContext) || defaultCastContext

const CastConsumer = castContext.Consumer

const useCastVolume = () => {
  const {
    state: {volume, muted},
    actions: {setVolume, setMute},
  } = useCastContext()

  return {
    volume,
    muted,
    changeVolume: setVolume,
    toggleMuted: () => setMute(!muted),
  }
}

export {castContext, useCastContext, useCastVolume, CastProvider, CastConsumer}
