/* eslint-disable react/prop-types */
/* @jsxImportSource @emotion/react */

import {ResizeObserver} from '@juggle/resize-observer'
import useDimensions from 'react-cool-dimensions'

import {useAutoHide} from 'Hooks'
import getComponents from 'player/getComponents'
import Message from 'context/I18n/Message'
import DefaultLayout from './DefaultLayout'
import BottomPanels from './BottomPanels'
import Subtitles from './Subtitles'

const sizes = {
  'small-embed': 200,
  embed: 400,
  'tablet-portrait': 600,
  'tablet-landscape': 900,
  desktop: 1200,
}

const PlayerUi = ({
  type = 'mobile',
  status,
  ready,
  seekable,
  focused,
  controlsDisplay,
  title,
  channelTitle,
  adStatus,
  subtitlesContent,
  adUrl,
  toolPanels,
  recommendation,
  activePanel,
  haveBottomItem,
  backRef,
  hidePanel,
  toggleRecommendationPanel,
  Layout = DefaultLayout,
  children,
}) => {
  const {mode, onClick, onMouseMove} = useAutoHide({
    pinned: status !== 'playing' || focused,
    tapToHide: type === 'mobile',
  })
  const {width, currentBreakpoint: size, ref: containerRef} = useDimensions({
    polyfill: ResizeObserver,
    breakpoints: sizes,
  })
  const {
    PlayButton,
    Seekbar,
    LiveSeekbar,
    RewindButton,
    ForwardButton,
    VolumeControl,
    FunctionButtons,
    FullScreenButton,
    PreviousEpisodeButton,
    NextEpisodeButton,
    BackButton,
    Loading,
    PlayPane,
    ErrorOverlay,
    LinearEndedOverlay,
    CastingOverlay,
    AdSkipButton,
  } = getComponents()
  const controlButtons = {
    playButton:
      type === 'mobile' && status === 'buffering' ? (
        <Loading
          styles={{
            position: 'static',
            width: '3em',
            margin: '1em',
            backgroundColor: 'transparent',
          }}
        />
      ) : (
        <PlayButton />
      ),
    ...(!adStatus &&
      seekable &&
      ready &&
      (type === 'mobile' || width >= sizes['tablet-portrait']) && {
        previousEpisodeButton: <PreviousEpisodeButton />,
        nextEpisodeButton: <NextEpisodeButton />,
        rewindButton: <RewindButton />,
        forwardButton: <ForwardButton />,
      }),
  }
  const backItems =
    status === 'buffering' ? (
      <Loading styles={{backgroundColor: 'transparent'}} />
    ) : (
      <PlayPane />
    )
  const uiElements = adStatus
    ? {
        adLink: adUrl && (
          <a href={adUrl} rel="noreferrer" target="_blank">
            <Message code="KKS.SSAI.LEARN.MORE" />
          </a>
        ),
        adStatus,
        adSkipButton: <AdSkipButton />,
      }
    : {
        title,
        channelTitle,
        seekbar: seekable ? <Seekbar /> : <LiveSeekbar />,
        functionButtons: ready &&
          (type === 'mobile' || width >= sizes['small-embed']) && (
            <FunctionButtons full={type === 'mobile' || width >= sizes.embed} />
          ),
      }

  return (
    <Layout
      type={type}
      display={mode}
      controlsDisplay={controlsDisplay || mode}
      size={size}
      haveBottomItem={haveBottomItem && !adStatus}
      {...uiElements}
      controlButtons={controlButtons}
      volumeControl={
        width >= sizes['small-embed'] && (
          <VolumeControl displaySlider={width >= 300} />
        )
      }
      fullscreenButton={ready && <FullScreenButton />}
      backButton={<BackButton />}
      backItems={backItems}
      containerRef={containerRef}
      backRef={backRef}
      {...{onClick, onMouseMove}}
    >
      <Subtitles text={subtitlesContent?.text} />
      {!adStatus && (
        <BottomPanels
          type={type}
          toolPanels={toolPanels}
          recommendation={recommendation}
          activePanel={activePanel}
          hidePanel={hidePanel}
          toggleRecommendationPanel={toggleRecommendationPanel}
        />
      )}
      {status === 'error' ? (
        <ErrorOverlay />
      ) : status === 'casting' ? (
        <CastingOverlay />
      ) : status === 'loading' ? (
        <Loading />
      ) : status === 'linearEnded' ? (
        <LinearEndedOverlay />
      ) : (
        ''
      )}
      {children}
    </Layout>
  )
}

export default PlayerUi
