/* eslint-disable indent */
import {useContext} from 'react'

import {getTargetQuality} from '../playerCore/adaptation'
import {usePlayer} from './playerContext'
import {Context} from './store'
import operator from './store/operator'

const shouldShowVersions = mediaSources =>
  mediaSources.length > 1 || mediaSources[0]
const shouldShowQuality = qualities => qualities.length > 1 || qualities[0]

const shouldShowSubtitles = subtitlesMenu =>
  subtitlesMenu.length > 0 || subtitlesMenu[0]

const shouldShowAudio = audioMenu => audioMenu.length > 1 || audioMenu[0]

const defaultQualityName = (items, quality = '') => {
  const valueMatch = items.find(
    it => it.value === quality || it.value === quality.default
  )
  if (valueMatch) {
    return valueMatch
  }
  const targetHeight = getTargetQuality(
    items.map(it => it.height),
    parseInt(quality.default || quality, 10)
  )
  return items.find(it => it.height === targetHeight)?.value
}

const speedItems = [0.25, 0.5, 0.75, 1, 1.25, 1.5, 1.75, 2].map(value => ({
  label: `${value}x`,
  value,
}))

const getSettingSections = ({
  contentType,
  mediaSources = [],
  playingSourceType,
  qualityItems = [],
  selectedQualityName,
  subtitleItems = [],
  selectedSubtitleName,
  audioItems = [],
  selectedAudioName,
  playbackRate,
}) =>
  [
    shouldShowVersions(mediaSources) && {
      section: 'mediaSource',
      title: 'KKS.SETTING.VERSION',
      selectedValue: playingSourceType,
      items: mediaSources.map(value => ({value})),
    },
    shouldShowQuality(qualityItems) && {
      section: 'quality',
      title: 'KKS.QUALITY',
      selectedValue: selectedQualityName,
      items: qualityItems,
    },
    shouldShowSubtitles(subtitleItems) && {
      section: 'subtitles',
      title: 'KKS.SUBTITLES',
      selectedValue: selectedSubtitleName,
      items: [...subtitleItems, {label: 'OFF', value: 'off'}],
    },
    shouldShowAudio(audioItems) && {
      section: 'audio',
      title: 'KKS.AUDIO',
      selectedValue: selectedAudioName,
      items: audioItems,
    },
    contentType !== 'lives' && {
      section: 'speed',
      title: 'KKS.SETTING.SPEED',
      selectedValue: playbackRate,
      items: speedItems,
    },
  ].filter(Boolean)

const useSettings = () => {
  const {options} = useContext(Context.Module)
  const {content, playingSourceType} = useContext(Context.API).state
  const {qualityItems, mediaSources} = useContext(Context.UI).state
  const allQualities =
    options.quality?.getQualityOptions?.(qualityItems) || qualityItems
  const defaultQuality =
    useContext(Context.UI).state.selectedQualityName ||
    defaultQualityName(allQualities, options.quality)
  const selectedQualityName = (
    allQualities.find(item => item.value === defaultQuality) ||
    allQualities[0] ||
    {}
  )?.value
  const player = usePlayer()
  const subtitleItems = player?.subtitles
    ?.list()
    // For missing tag CLOSED-CAPTIONS=NONE in .m3u8
    .filter(track => track.lang !== 'unknown')
    .map(track => ({label: track.label, value: track.lang}))
  let selectedSubtitleName = player?.subtitles
    ?.list()
    .find(track => track.enabled === true)?.lang
  if (!selectedSubtitleName) selectedSubtitleName = 'off'

  const audioItems = player
    ?.getAvailableAudio()
    .filter(track => track.lang && track.lang !== 'und')
    .map(track => ({label: track.label, value: track.lang}))

  return getSettingSections({
    contentType: content?.contentType,
    mediaSources,
    playingSourceType,
    qualityItems: allQualities,
    selectedQualityName,
    subtitleItems,
    selectedSubtitleName,
    audioItems,
    selectedAudioName: player?.getAudio()?.lang,
    playbackRate: player?.getPlaybackSpeed(),
  })
}

const useChangeSettings = () => {
  const {dispatch} = useContext(Context.Module)
  const player = usePlayer()

  return ({section, value}) => {
    if (section === 'mediaSource' || !player.hasEnded()) {
      player.play()
    }
    if (section === 'quality') {
      dispatch(operator.changeQuality(value))
    }
    if (section === 'mediaSource') {
      dispatch(operator.changeMediaSource(value))
    }
    if (section === 'speed') {
      player.setPlaybackSpeed(value)
    }
    if (section === 'subtitles') {
      const subtitles = player.subtitles
      subtitles.list().forEach(track => {
        const setter =
          value !== track.lang ? subtitles.disable : subtitles.enable
        setter(track.id)

        // Safari need to fire cueExit manually.
        if (value === 'off') subtitles.cueExit()
      })
    }
    if (section === 'audio') {
      const {id} = player
        .getAvailableAudio()
        .find(track => track.lang === value)
      player.setAudio(id)
    }
  }
}

export {useChangeSettings, useSettings, getSettingSections}
