/* @jsxImportSource @emotion/react */
import PropTypes from 'prop-types'

import icon from 'style/icon'

const sizes = {
  normal: {
    bottom: '1rem',
    padding: '0.5rem',
    width: '18.5rem',
    height: '5.25rem',
    fontSize: '12px',
    '--spacing': '0rem',
  },
  big: {
    bottom: '5rem',
    padding: '0.75rem',
    width: '32rem',
    height: '10rem',
    fontSize: '20px',
    '--spacing': '0.5rem',
  },
}

const style = {
  container: {
    position: 'absolute',
    zIndex: '-1',
    right: '0',
    display: 'flex',
    borderRadius: '4px',
    color: '#fff',
    backgroundColor: 'rgba(20, 20, 20, 0.8)',
    opacity: '0',
    transition: 'opacity 1s ease',
  },
  opening: {
    zIndex: 'inherit',
    opacity: '1',
  },
  cover: imageUrl => ({
    flex: '0 40%',
    border: 'none',
    borderRadius: '4px',
    background: `
      center / 33% no-repeat url(${icon.playCircleBorder}),
      ${imageUrl ? `center / cover url(${imageUrl}),` : ''} 
      #ccc
    `,
  }),
  info: {
    marginLeft: '0.5rem',
    flex: '1',
  },
  message: {
    marginBottom: ['1rem', 'var(--spacing)'],
    display: 'flex',
  },
  dismiss: {
    marginLeft: 'auto',
    width: ['2rem', 'calc(1.5rem + var(--spacing))'],
    height: ['2rem', 'calc(1.5rem + var(--spacing))'],
    border: 'none',
    background: `center / 1rem no-repeat url(${icon.close}), transparent`,
  },
}

const PlayDialog = ({
  opening,
  coverImageUrl,
  message,
  title,
  size,
  containerRef,
  play,
  dismiss,
  ...rest
}) => (
  <div
    css={[style.container, sizes[size], opening && style.opening]}
    ref={containerRef}
    {...rest}
  >
    <button
      css={style.cover(coverImageUrl)}
      onClick={play}
      className="kks-player__next-episode-button"
    />
    <div css={style.info}>
      <div css={style.message}>
        {message}
        <button
          css={style.dismiss}
          onClick={dismiss}
          className="kks-player__cancel-autoplay-button"
        />
      </div>
      {title}
    </div>
  </div>
)

PlayDialog.propTypes = {
  opening: PropTypes.bool,
  coverImageUrl: PropTypes.string,
  message: PropTypes.node,
  title: PropTypes.node,
  size: PropTypes.string,
  containerRef: PropTypes.object,
  play: PropTypes.func,
  dismiss: PropTypes.func,
}

export default PlayDialog
