import {useContext} from 'react'
import PropTypes from 'prop-types'
import _get from 'dlv'

import icon from 'style/icon'
import Tooltip from 'player/container/ControlsTooltip'
import Message from 'context/I18n/Message'
import {Context} from 'player/store/index'
import Action from 'player/store/action/UI'
import IconButton from './IconButton'

const PreEpisodeButton = ({style}) => {
  const {
    state: {content},
    dispatch,
  } = useContext(Context.API)
  const preEpisode = _get(content, 'recommend.previous.id', null)

  return (
    <Tooltip title={<Message code="KKS.PLAYER.PREVIOUS" />} bottom="48px">
      <IconButton
        style={style}
        name="kks-player__previous-episode-button"
        src={icon.previousEpisode}
        disabled={!preEpisode}
        onClick={() =>
          preEpisode && dispatch(Action.changePreviousEpisode(preEpisode))
        }
      />
    </Tooltip>
  )
}

PreEpisodeButton.propTypes = {
  keep: PropTypes.bool,
  style: PropTypes.object,
}

export default PreEpisodeButton
