import PropTypes from 'prop-types'
import {mask} from 'style'
import icon from 'style/icon'
import Icon from 'component/Icon'
import ErrorMessage from 'component/ErrorMessage'
import TextButton from 'component/Button/TextButton'

const _css = {
  icon: {
    width: '78px',
    height: '78px',
  },
}

const Error = ({error, style, onBack}) => (
  <div className="kks-player__error" css={[mask, style]}>
    <Icon style={_css.icon} src={icon.warning} />
    <ErrorMessage errorAction={error[error.length - 1]} />
    <TextButton
      name="kks-player__error__back-button"
      text="KKS.BACK"
      defaultValue="BACK"
      onClick={onBack}
    />
  </div>
)
Error.propTypes = {
  error: PropTypes.array,
  style: PropTypes.object,
  onBack: PropTypes.func,
}
Error.defaultProps = {
  error: [],
  style: {},
  onBack: () => {},
}

export default Error
