/* @jsxImportSource @emotion/react */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import {cloneElement} from 'react'
import PropTypes from 'prop-types'

import RecommendationPanel from 'component/Panel/RecommendationPanel'

const panelStyle = {
  alignSelf: 'center',
  display: 'flex',
  flexDirection: 'column',
  overflow: 'visible',
  maxWidth: '100%',
  position: 'absolute',
  bottom: '0',
  transition: 'transform 0s 0.5s',
  transform: 'translateY(100%)',
  '> *:last-of-type': {
    flex: '0 auto',
    transition: 'transform 0.5s ease, opacity 0.5s ease',
    transform: 'translateY(100%)',
    opacity: '0',
  },
}

const panelOpenStyle = {
  transition: 'transform 0s 0s',
  transform: 'translateY(0)',
  '> *:last-of-type': {
    transform: 'translateY(0)',
    opacity: '1',
  },
}

// this is for replacing recommendation panel in future
const panelClosedStyle = height =>
  height && {
    '> *:last-of-type': {
      // workaround IE 11 CSS calc() bug
      transform: `translateY(100%) translateY(-${height})`,
    },
  }

const backdropStyle = {
  height: '100%',
  backgroundColor: 'rgba(0, 0, 0, 0)',
  transition: 'background-color 0.5s ease, transform 0s 0.5s',
}

const backdropOpenStyle = {
  backgroundColor: 'rgba(0, 0, 0, 0.6)',
  transition: 'background-color 0.5s ease',
}

const BottomPanels = ({
  type = 'mobile',
  recommendation,
  toolPanels,
  activePanel,
  hidePanel,
  toggleRecommendationPanel,
}) => (
  <>
    {recommendation && type === 'desktop' && (
      <>
        <div style={{height: '4rem'}} />
        <RecommendationPanel
          title={recommendation.title}
          opening={activePanel === 'recommendation'}
          toggle={toggleRecommendationPanel}
        >
          {recommendation.content}
        </RecommendationPanel>
      </>
    )}
    {toolPanels.map(
      (
        {
          content,
          open,
          style,
          hasBackdrop = true,
          pinned = false,
          foldedHeight = 0,
        },
        index
      ) => {
        const panelOpen = open || index === activePanel

        return (
          <div
            css={[
              panelStyle,
              panelOpen ? panelOpenStyle : panelClosedStyle(foldedHeight),
              hasBackdrop && [backdropStyle, panelOpen && backdropOpenStyle],
              panelOpen && style,
            ]}
            className={pinned && 'pinned'}
          >
            {hasBackdrop && (
              <div
                style={{flex: 1}}
                onClick={event => {
                  event.stopPropagation()
                  hidePanel(event)
                }}
              />
            )}
            {cloneElement(content[0] || content, {
              active: panelOpen,
              close: hidePanel,
            })}
          </div>
        )
      }
    )}
  </>
)

BottomPanels.propTypes = {
  type: PropTypes.oneOf(['mobile', 'desktop']),
  recommendation: PropTypes.object,
  toolPanels: PropTypes.array,
  activePanel: PropTypes.string,
  hidePanel: PropTypes.func,
  toggleRecommendationPanel: PropTypes.func,
}

export default BottomPanels
