import {type} from '../middleware/localStorage'
import {StorageKey} from 'Enum'

const initState = {
  [StorageKey.VOLUME]: {
    volume: 100,
    muted: false,
  },
}

export default (state = initState, action) => {
  switch (action.type) {
    case type.LOAD:
      return {...state, [action.key]: action.value}
    case type.SAVE:
      return {...state, [action.key]: action.value}
  }
  return state
}
