import _get from 'dlv'
import {useState, useEffect, useContext} from 'react'
import {ResizeObserver} from '@juggle/resize-observer'
import useDimensions from 'react-cool-dimensions'

import {useUiRef} from 'player/playerContext'
import PlayDialog from 'component/PlayDialog'
import {Context} from 'player/store'
import UIAction from 'player/store/action/UI'
import I18n from 'context/I18n'
import {ModuleState} from 'Enum'

const useCountdownSecond = ({time, enabled}) => {
  const [timeLeft, setTimeLeft] = useState()

  useEffect(() => {
    if (!enabled) return

    setTimeLeft(time)
    const intervalId = setInterval(
      () => setTimeLeft(current => current - 1),
      1000
    )

    return () => clearInterval(intervalId)
  }, [time, enabled])

  return timeLeft
}

const AutoplayDialog = () => {
  const {currentBreakpoint} = useDimensions({
    ref: useUiRef(),
    polyfill: ResizeObserver,
    breakpoints: {
      normal: 0,
      big: 600,
    },
  })
  const {
    state: {autoplayDialogOpening: opening},
    dispatch,
  } = useContext(Context.UI)
  const {
    state: {content},
  } = useContext(Context.API)
  const {
    state: {state: moduleState},
  } = useContext(Context.Module)
  const {
    id,
    title,
    images: [{url: coverImageUrl}],
  } = _get(content, 'recommend.next') || {images: [{}]}
  const playNext = () => {
    dispatch(UIAction.changeNextEpisode(id))
  }
  const timeLeft = useCountdownSecond({time: 10, enabled: opening})

  useEffect(() => {
    if (timeLeft <= 0) playNext()
  }, [timeLeft > 0])

  return (
    <PlayDialog
      className="kks-player__autoplay pinned"
      opening={opening && moduleState !== ModuleState.ERROR}
      message={
        <I18n.Message code="KKS.ENDROLL.COUNTDOWN" property={{timeLeft}} />
      }
      title={title}
      coverImageUrl={coverImageUrl}
      size={currentBreakpoint}
      play={playNext}
      dismiss={() => dispatch(UIAction.dismissAutoplay())}
    />
  )
}

export default AutoplayDialog
