import _get from 'dlv'

export const combineReducer = structure => {
  const props = Object.keys(structure)
  return (state, action) => {
    const newState = props.reduce((res, name) => {
      res[name] = structure[name](state[name], action)
      return res
    }, {})
    const isChange = props.some(name => state[name] !== newState[name])
    return isChange ? newState : state
  }
}

const queryString = object =>
  object &&
  typeof object === 'object' &&
  Object.getOwnPropertyNames(object)
    .map(name => `${name}=${encodeURIComponent(object[name])}`)
    .join('&')

export const convertToSearch = query =>
  Object.entries(query)
    .map(([key, value]) => {
      const jsonString = JSON.stringify(value)
      const encodedValue = ['string'].includes(typeof value)
        ? encodeURIComponent(jsonString)
        : jsonString
      return [encodeURIComponent(key), encodedValue]
    })
    .reduce((res, [key, value]) => `${res}${key}=${value}&`, '?')
    .slice(0, -1)

const toKeyValuePair = (object = {}) =>
  Object.entries(object)
    .map(([key, value]) => `${key}: ${value}`)
    .join('\n')

export const mapPlayerOptions = values => ({
  ...values,
  contentOther: toKeyValuePair(values.contentOther),
  langCustomCode: toKeyValuePair(values.langCustomCode),
  customHeader: toKeyValuePair(values.customHeader),
  ampProps: toKeyValuePair(values.ampProps),
  supportEnvironmentList: JSON.stringify(values.supportEnvironmentList),
})

export const catchBy = error => func => (...args) => {
  try {
    return func.apply(this, args)
  } catch (e) {
    return error(e)
  }
}

export function scope(min, value, max) {
  if (min > value) return min
  if (max < value) return max
  return value
}

export const getLiveTime = detail => {
  const startTime = _get(detail, 'start_time') || _get(detail, 'startTime') || 0
  const endTime = _get(detail, 'end_time') || _get(detail, 'endTime') || 0
  const duration = endTime - startTime
  const nowSecond = Math.floor(Date.now() / 1000)

  return {
    currentTime: scope(0, nowSecond - startTime, duration),
    duration,
  }
}

export function convertToTimeString(date, fmt) {
  return fmt
    .replace(/YYYY/g, `0000${date.getFullYear()}`.slice(-4))
    .replace(/MM/g, `00${date.getMonth() + 1}`.slice(-2))
    .replace(/DD/g, `00${date.getDate()}`.slice(-2))
    .replace(/HH/g, `00${date.getHours()}`.slice(-2))
    .replace(/mm/g, `00${date.getMinutes()}`.slice(-2))
    .replace(/SS/g, `00${date.getSeconds()}`.slice(-2))
}

export function convertToSeconds(timeString) {
  const [hours, minutes, seconds] = timeString.split(':').map(parseFloat)
  return hours * 3600 + minutes * 60 + seconds
}

export function isInScope(min, value, max) {
  return min <= value && value <= max
}

export function getVersion() {
  try {
    // eslint-disable-next-line no-undef
    return process.env.VERSION
  } catch (e) {
    return undefined
  }
}

export function getPopoverPosition(rect, target, boundary) {
  const rectX = rect.x || rect.left
  const boundaryX = boundary.x || boundary.left
  const maxLeft = boundary.width - rect.width
  const targetCenter = (target.left + target.right) / 2 - boundaryX
  const center = rectX + rect.width / 2 - boundaryX
  const alignLeft = rectX + (targetCenter - center) - boundaryX
  return {
    left: Math.max(0, Math.min(alignLeft, maxLeft)),
    top: target.top - rect.height,
  }
}

// eslint-disable-next-line consistent-return
export const safeExec = (func, args = []) => {
  try {
    return func && func(...args)
  } catch (e) {
    console.warn(e)
  }
}

export const debounce = (callback, ms) => {
  let lastTouchEnd = 0
  return event => {
    const now = new Date().getTime()
    if (now - lastTouchEnd <= ms) {
      event.preventDefault()
    } else {
      callback(event)
      lastTouchEnd = now
    }
  }
}

export {queryString, queryString as convertObjectToQueryString}
