export const type = {
  LOAD: 'LOCAL_STORAGE_LOAD',
  SAVE: 'LOCAL_STORAGE_SAVE',
}

export const action = {
  load: key => ({type: type.LOAD, key}),
  save: (key, value) => ({type: type.SAVE, key, value}),
}

const getLocalStorageValue = (key, defaultValue) => {
  try {
    const source = localStorage.getItem(key)
    if (typeof source === 'string') {
      return JSON.parse(source)
    }
    // eslint-disable-next-line no-empty
  } catch (error) {}
  return defaultValue
}

const setLocalStorageValue = (key, value) => {
  localStorage.setItem(key, JSON.stringify(value))
}

const _localStorage = () => next => action => {
  switch (action.type) {
    case type.LOAD:
      action.value = getLocalStorageValue(action.key)
      break
    case type.SAVE:
      setLocalStorageValue(action.key, action.value)
      break
  }

  return next(action)
}

export default _localStorage
