/* eslint-disable indent */
/* @jsxImportSource @emotion/react */
import icon from 'style/icon'
import Tooltip from 'component/Tooltip'
import Message from 'context/I18n/Message'
import SimpleSlider from 'component/SimpleSlider'
import Icon from './Icon'
import Button from './Button/SquareButton'

const style = {
  display: 'inline-flex',
  alignItems: 'center',
}

/* eslint-disable react/prop-types */
const VolumeControl = ({
  useVolumeState,
  displaySlider = false,
  max = 1,
  TooltipComponent = Tooltip,
  /* eslint-disable react/prop-types */
}) => {
  const {volume, muted, changeVolume, toggleMuted} = useVolumeState()
  const iconName =
    muted || volume === 0
      ? icon.mute
      : volume < max / 2
      ? icon.volumeLow
      : icon.volumeHight

  return (
    <div className="kks-player__volume" css={style}>
      <TooltipComponent
        title={
          <Message code={muted ? 'KKS.PLAYER.UNMUTE' : 'KKS.PLAYER.MUTE'} />
        }
        bottom="48px"
      >
        <Button
          name="kks-player__volume-button"
          tooltip={muted ? 'KKS.PLAYER.UNMUTE' : 'KKS.PLAYER.MUTE'}
          onClick={toggleMuted}
        >
          <Icon src={iconName} />
        </Button>
      </TooltipComponent>
      {displaySlider && (
        <SimpleSlider
          css={{
            marginLeft: '0.8rem',
            width: '4em',
          }}
          value={muted ? 0 : volume}
          max={max}
          onChange={(_, {value}) => changeVolume(value)}
        />
      )}
    </div>
  )
}

export default VolumeControl
