import {cloneElement} from 'react'
import PropTypes from 'prop-types'
import {useContext} from 'react'

import {useOptions} from 'player/playerContext'
import {Context} from 'player/store/index'
import action from 'player/store/action/UI'

import SettingButton from './Button/SettingButton'
import CastButton from './Button/CastButton'

const FunctionButtons = ({full = true}) => {
  const {toolPanels = []} = useOptions()
  const {dispatch} = useContext(Context.Module)

  return (
    <>
      {full && (
        <>
          <CastButton />
          {toolPanels.map((item, index) =>
            cloneElement(item.button, {
              key: index,
              onClick: () => {
                dispatch(action.openPanel(index))
              },
            })
          )}
        </>
      )}
      <SettingButton />
    </>
  )
}

FunctionButtons.propTypes = {
  full: PropTypes.bool,
}

export default FunctionButtons
