/* @jsxImportSource @emotion/react */
import PropTypes from 'prop-types'

import icon from 'style/icon'
import Icon from 'component/Icon'
import Message from 'context/I18n/Message'

const styles = {
  padding: '0.5rem',
  border: '1px solid #fff',
  color: '#fff',
  background: 'rgba(0, 0, 0, 0.4)',
  fontSize: '24px',
  opacity: 0.8,
  'span:last-of-type': {
    marginLeft: '1rem',
    width: '16px',
    height: '16px',
    display: 'inline-block',
  },
}

const SkipButton = ({remainingTime, onClick}) => (
  <button css={styles} disabled={remainingTime > 0} onClick={onClick}>
    {remainingTime > 0 ? (
      <>
        {Math.ceil(remainingTime)} <Message code="KKS.SSAI.SECONDS" />
        <Icon src={icon.nextEpisode} style={{opacity: 0.24}} />
      </>
    ) : (
      <>
        <Message code="KKS.SSAI.SKIP.AD" />
        <Icon src={icon.nextEpisode} />
      </>
    )}
  </button>
)

SkipButton.propTypes = {
  remainingTime: PropTypes.number,
  onClick: PropTypes.func,
}

export default SkipButton
