/* eslint-disable indent */
// Refactor is in progress, old player UI components will be imported from here temporarily
import {useState, useEffect, useContext} from 'react'
import shallow from 'zustand/shallow'

import useStore from './store/useStore'
import getPlaybackState from 'player/store/getPlaybackState'
import {useOptions} from './playerContext'
import PlayButton from 'player/container/Button/PlayButton'
import Seekbar from 'player/container/Seekbar'
import LiveSeekbar from 'player/container/LiveSeekbar'
import RewindButton from 'player/container/Button/RewindButton'
import ForwardButton from 'player/container/Button/ForwardButton'
import VolumeControl from 'player/container/VolumeControl'
import FunctionButtons from 'player/container/FunctionButtons'
import FullScreenButton from 'player/container/Button/FullScreenButton'
import PreviousEpisodeButton from 'player/container/Button/PreEpisodeButton'
import NextEpisodeButton from 'player/container/Button/NextEpisodeButton'
import BackButton from 'player/container/Button/BackButton'
import CastButton from 'player/container/Button/CastButton'
import Error from 'component/Mask/Error'
import Loading from 'component/Mask/Loading'
import PlayPanel from 'component/Panel/PlayPanel'
import LinearEnded from 'component/Mask/LinearEnd'
import Casting from 'component/Mask/Cast'
import SkipButton from 'component/Button/SkipButton'
import {Context} from 'player/store/index'
import Action from 'player/store/action/index'
import playAction from 'player/store/action/playAction'
import UiAction from 'player/store/action/UI'
import operator from 'player/store/operator'

const PlayPane = () => {
  const {status} = useStore(getPlaybackState, shallow)
  const type = useStore(({UI: state}) =>
    state.activePanel === 'recommendation'
      ? 'dismiss'
      : state.autoplayDialogOpening || state.autoplayDialogOpening
      ? 'none'
      : 'play'
  )
  const adUrl = useStore(({UI: state}) => state.ad?.clickThroughUrl)
  const {dispatch} = useContext(Context.PlayerState)

  return type === 'dismiss' ? (
    <div
      style={{
        height: '100%',
        width: '100%',
        background: 'rgba(0, 0, 0, 0.6)',
      }}
      onClick={() => dispatch(UiAction.hidePanel())}
    />
  ) : (
    type === 'play' && (
      <PlayPanel
        isPlaying={/playing|loading/.test(status)}
        href={adUrl}
        onClick={() => dispatch(playAction(status))}
      />
    )
  )
}

const ErrorOverlay = () => {
  const {dispatch} = useContext(Context.Module)
  const {error} = useContext(Context.Module).state

  return <Error error={error} onBack={() => dispatch(Action.back())} />
}

const CastingOverlay = () => {
  const {dispatch} = useContext(Context.Module)

  return <Casting onBack={() => dispatch(Action.back())} />
}

const LinearEndedOverlay = () => {
  const {dispatch} = useContext(Context.Module)

  return (
    <LinearEnded
      onRetry={() => dispatch(operator.retry)}
      onLeave={() => dispatch(Action.back())}
    />
  )
}

const getAdSkipTimeOffset = ({API, UI}) =>
  API.content?.contentType === 'lives' ? -1 : UI.ad?.skipTimeOffset

const AdSkipButton = () => {
  const {plugins} = useOptions()
  const skipTimeOffset = useStore(getAdSkipTimeOffset)
  const [streamTime, setStreamTime] = useState(0)

  useEffect(() => {
    const video = document.querySelector('video')
    const upadteTime = () => setStreamTime(() => video.currentTime)
    video.addEventListener('timeupdate', upadteTime)

    return () => video.removeEventListener('timeupdate', upadteTime)
  }, [])

  return (
    skipTimeOffset >= 0 && (
      <SkipButton
        remainingTime={skipTimeOffset - streamTime}
        onClick={() => plugins.forEach(plugin => plugin.skipAd?.())}
      />
    )
  )
}

const getComponents = () => ({
  PlayButton,
  Seekbar,
  LiveSeekbar,
  RewindButton,
  ForwardButton,
  VolumeControl,
  FunctionButtons,
  FullScreenButton,
  PreviousEpisodeButton,
  NextEpisodeButton,
  BackButton,
  Loading,
  PlayPane,
  ErrorOverlay,
  LinearEndedOverlay,
  CastingOverlay,

  AdSkipButton,
  CastButton,
})

export default getComponents
