import {useContext} from 'react'
import PropTypes from 'prop-types'

import Tooltip from 'component/Tooltip'
import {Context} from 'player/store'
import {useUiContainer} from 'player/playerContext'

const ControlsTooltip = ({children, ...props}) => {
  const {state = {}} = useContext(Context.UI) || {}
  const uiContainer = useUiContainer()

  // follows YouTube UX, tooltips are not shown while settings open
  return state.isSettingsOpen ? (
    children
  ) : (
    <Tooltip {...props} container={uiContainer}>
      {children}
    </Tooltip>
  )
}

ControlsTooltip.propTypes = {
  children: PropTypes.node,
}

export default ControlsTooltip
