/* @jsxImportSource @emotion/react */
import {keyframes} from '@emotion/react'
import {useState} from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import {layer} from 'style'
import icon from 'style/icon'

const fadeout = keyframes`
  0% {
    background-size: 140px 140px;
    opacity:1;
  }
  100% {
    background-size: 160px 160px;
    opacity:0;
  }
`

const _css = (animating, src) => ({
  ...(animating && {
    animationName: fadeout,
  }),
  backgroundSize: '140px 140px',
  backgroundPosition: 'center',
  backgroundRepeat: 'no-repeat',
  animationFillMode: 'forwards',
  animationDelay: '0.3s',
  animationDuration: '300ms',
  backgroundImage: `url(${src})`,
  opacity: 0,
})

const PlayPanel = ({isPlaying, href, onClick}) => {
  const [animating, setAnimating] = useState(false)

  return href ? (
    <a href={href} target="_blank" rel="noreferrer" css={layer} />
  ) : (
    <div
      className={classnames('kks-player__play-panel', {
        [`kks-player__play-panel--${isPlaying ? 'play' : 'pause'}`]: animating,
      })}
      css={[
        layer,
        _css(animating, isPlaying ? icon.playCircle : icon.pauseCircle),
      ]}
      onAnimationEnd={() => setAnimating(false)}
      onClick={() => {
        setAnimating(true)
        onClick()
      }}
    />
  )
}

PlayPanel.propTypes = {
  isPlaying: PropTypes.bool,
  href: PropTypes.string,
  onClick: PropTypes.func,
}

export default PlayPanel
