/* @jsxImportSource @emotion/react */
import PropTypes from 'prop-types'

import {button} from 'style'

const barStyle = {
  padding: '36px 18px',
  // Prevent overflow when content text is too long
  minWidth: 0,
  display: 'flex',
  flexWrap: 'wrap',
  alignItems: 'center',
  '> *': {
    // margin: '0 18px' might override seekbar margin
    marginLeft: '18px',
    marginRight: '18px',
  },
  opacity: '1',
  transition: 'opacity 0.5s ease',
}

const fadeStyle = {
  opacity: '0',
  // disable all UI interactions while hidden
  pointerEvents: 'none',
  touchAction: 'none',
}

const FunctionBar = ({style, fade, children}) => (
  <div
    className="kks-player__function-bar"
    css={[barStyle, fade && fadeStyle, style, {button}]}
  >
    {children}
  </div>
)

FunctionBar.propTypes = {
  style: PropTypes.object,
  fade: PropTypes.bool,
  children: PropTypes.node,
}
FunctionBar.defaultProps = {
  style: {},
}

export const style = {
  top: {
    flexWrap: 'nowrap',
    backgroundImage: `
      linear-gradient(
        to top,
        rgba(0, 0, 0, 0),
        rgba(0, 0, 0, 0.8)
      )
    `,
  },
  bottom: {
    padding: '0 18px 36px',
  },
}

export default FunctionBar
