import API from './API'
import Player from './Player'
import UI from './UI'
import LocalStorage from './LocalStorage'
import {combineReducer} from 'util/index'
import {type} from '../action'
import {ModuleState, CastState} from 'Enum'

export const initState = {
  state: ModuleState.UNINITIALIZATION,
  castState: CastState.NOT_CONNECTED,
  error: [],
}

const Module = (state = initState, action) => {
  switch (action.type) {
    case type.INITIALIZE:
      return {...state, state: ModuleState.INITIALIZING}
    case type.INITIALIZED:
      return {...state, state: ModuleState.INITIALIZED}
    case type.LOAD_CONTENT:
      return {...state, state: ModuleState.LOADING}
    case type.READY_TO_PLAY:
      return {...state, state: ModuleState.READY}
    case type.CAST_STATE_CHAGNE:
      return {...state, castState: action.castState}
    case type.ERROR:
      return {state: ModuleState.ERROR, error: state.error.concat(action)}
    default:
      return state
  }
}

export default combineReducer({
  Module,
  LocalStorage,
  API,
  Player,
  UI,
})
