import Action from './index'

export const type = {
  MOUNT: 'PLAYER_MOUNT',
  MOUNTED: 'PLAYER_MOUNTED',
  LOAD: 'PLAYER_LOAD',
  LOADED: 'PLAYER_LOADED',
  UNLOAD: 'PLAYER_UNLOAD',
  UNLOADED: 'PLAYER_UNLOADED',
  READY: 'PLAYER_READY',
  FETCH_STATE: 'PLAYER_FETCH_STATE',
  SET_STATE: 'PLAYER_SET_STATE',
  UNMOUNT: 'PLAYER_UNMOUNT',

  PLAY: 'PLAYER_PLAY',
  PLAYING: 'PLAYER_PLAYING',
  PAUSE: 'PLAYER_PAUSE',
  PAUSED: 'PLAYER_PAUSED',
  ENDED: 'PLAYER_ENDED',
  UPDATE_CURRENT_TIME: 'PLAYER_UPDATE_CURRENT_TIME',
  SEEK: 'PLAYER_SEEK',
  SEEKED: 'PLAYER_SEEKED',
  UPDATE_VOLUME: 'PLAYER_UPDATE_VOLUME',
  SET_VOLUME: 'PLAYER_SET_VOLUME',
  MUTE: 'PLAYER_MUTE',
  MUTED: 'PLAYER_MUTED',
  UNMUTE: 'PLAYER_UNMUTE',
  UNMUTED: 'PLAYER_UNMUTED',
  ENTER_FULL_SCREEN: 'PLAYER_ENTER_FULL_SCREEN',
  LEAVE_FULL_SCREEN: 'PLAYER_LEAVE_FULL_SCREEN',
  UPDATE_VIEW_MODE: 'PLAYER_UPDATE_VIEW_MODE',
  UPDATE_BUFFER_TIME: 'PLAYER_UPDATE_BUFFER_TIME',
  STALLING: 'PLAYER_STALLING',
  STALLED: 'PLAYER_STALLED',
  CHANGE_DASH_QUALITY: 'PLAYER_CHANGE_DASH_QUALITY',
}
export default {
  mount: () => ({type: type.MOUNT}),
  mounted: () => ({type: type.MOUNTED}),
  load: () => ({type: type.LOAD}),
  loaded: payload => ({type: type.LOADED, payload}),
  unload: () => ({type: type.UNLOAD}),
  unloaded: () => ({type: type.UNLOADED}),
  ready: () => ({type: type.READY}),
  fetchState: () => ({type: type.FETCH_STATE}),
  setState: state => ({type: type.SET_STATE, state}),
  unmount: () => ({type: type.UNMOUNT}),

  play: () => ({type: type.PLAY}),
  playing: () => ({type: type.PLAYING}),
  pause: () => ({type: type.PAUSE}),
  paused: () => ({type: type.PAUSED}),
  ended: () => ({type: type.ENDED}),
  updateCurrentTime: ({currentTime: time, adRemainingTime}) => ({
    type: type.UPDATE_CURRENT_TIME,
    time,
    adRemainingTime,
  }),
  seek: time => ({type: type.SEEK, time}),
  seeked: time => ({type: type.SEEKED, time}),
  setVolume: volume => ({type: type.SET_VOLUME, volume}),
  updateVolume: volume => ({type: type.UPDATE_VOLUME, volume}),
  mute: () => ({type: type.MUTE}),
  muted: () => ({type: type.MUTED}),
  unmute: () => ({type: type.UNMUTE}),
  unmuted: () => ({type: type.UNMUTED}),
  enterFullScreen: () => ({type: type.ENTER_FULL_SCREEN}),
  leaveFullScreen: () => ({type: type.LEAVE_FULL_SCREEN}),
  updateViewMode: mode => ({type: type.UPDATE_VIEW_MODE, mode}),
  updateBufferTime: time => ({type: type.UPDATE_BUFFER_TIME, time}),
  stalling: () => ({type: type.STALLING}),
  stalled: () => ({type: type.STALLED}),
  changeDashQuality: () => ({type: type.CHANGE_DASH_QUALITY}),

  error: error => Action.error('Player', error),
}
