import {useContext} from 'react'
import PropTypes from 'prop-types'
import _get from 'dlv'

import icon from 'style/icon'
import Tooltip from 'player/container/ControlsTooltip'
import Message from 'context/I18n/Message'
import {Context} from 'player/store/index'
import Action from 'player/store/action/UI'
import IconButton from './IconButton'

const NextEpisodeButton = ({style}) => {
  const {
    state: {content},
    dispatch,
  } = useContext(Context.API)
  const nextEpisode = _get(content, 'recommend.next.id', null)

  return (
    <Tooltip title={<Message code="KKS.PLAYER.NEXT" />} bottom="48px">
      <IconButton
        style={style}
        name="kks-player__next-episode-button"
        src={icon.nextEpisode}
        disabled={!nextEpisode}
        onClick={() =>
          nextEpisode && dispatch(Action.changeNextEpisode(nextEpisode))
        }
      />
    </Tooltip>
  )
}

NextEpisodeButton.propTypes = {
  keep: PropTypes.bool,
  style: PropTypes.object,
}

export default NextEpisodeButton
