/* @jsxImportSource @emotion/react */
import PropTypes from 'prop-types'
import shallow from 'zustand/shallow'

import useStore from 'player/store/useStore'
import {useOptions} from 'player/playerContext'
import getPlaybackInfo from 'player/store/getPlaybackInfo'
import useUiActions from 'player/store/useUiActions'
import {isDesktop} from 'util/environment'
import CoverImageWrap from 'player/container/CoverImageWrap'
import SimplePlayerUi from 'playerUi/index'
import AutoplayDialog from './AutoplayDialog'
import PlayerSettings from './PlayerSettings'

const PlayerUi = ({coverImageUrl, backRef}) => {
  const {recommendation, toolPanels} = useOptions()

  return (
    <CoverImageWrap url={coverImageUrl}>
      <SimplePlayerUi
        type={isDesktop() ? 'desktop' : 'mobile'}
        {...useStore(getPlaybackInfo, shallow)}
        backRef={backRef}
        toolPanels={toolPanels}
        recommendation={recommendation}
        {...useUiActions()}
        haveBottomItem={Boolean(recommendation)}
      >
        <PlayerSettings
          desktop={isDesktop()}
          hasBottomPanel={Boolean(recommendation)}
        />
      </SimplePlayerUi>
      <AutoplayDialog />
    </CoverImageWrap>
  )
}

PlayerUi.propTypes = {
  coverImageUrl: PropTypes.string,
  backRef: PropTypes.object,
}

export default PlayerUi
