/* eslint-disable indent */
import {useContext} from 'react'
import {cx} from '@emotion/css'
import shallow from 'zustand/shallow'

import icon from 'style/icon'
import useStore from 'player/store/useStore'
import getPlaybackState from 'player/store/getPlaybackState'
import Tooltip from 'player/container/ControlsTooltip'
import Message from 'context/I18n/Message'
import {Context} from 'player/store/index'
import playAction from 'player/store/action/playAction'
import {isDesktop} from 'util/environment'
import IconButton from './IconButton'
import {useDebounce} from 'Hooks'

const PlayButton = () => {
  const playbackState = useStore(getPlaybackState, shallow)
  const disabled = !isDesktop() && playbackState.loading
  const status = useDebounce({delay: 200, value: playbackState.status})
  const {dispatch} = useContext(Context.PlayerState)

  const buttonProps = (() => {
    switch (status) {
      case 'playing':
        return {src: icon.pause, tooltip: 'KKS.PLAYER.PAUSE'}
      case 'replay':
        return {src: icon.replay, tooltip: 'KKS.PLAYER.REPLAY'}
      default:
        return {src: icon.play, tooltip: 'KKS.PLAYER.PLAY'}
    }
  })()

  return (
    <Tooltip title={<Message code={buttonProps.tooltip} />} bottom="48px">
      <IconButton
        name={cx(
          'play-button',
          'kks-player__play-button',
          `kks-player__play-button--${status}`
        )}
        disabled={disabled}
        {...buttonProps}
        onClick={() => dispatch(playAction(status))}
      />
    </Tooltip>
  )
}

export default PlayButton
