import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'

import {linkPluginEvents} from 'playerCore/events'
import uiAction from './action/UI'
import {useCache} from 'Hooks'
import {useDispatchSessionEvent} from 'player/playerContext'

export const Context = {
  Adapter: React.createContext(),
  Full: React.createContext(),
  Module: React.createContext(),
  LocalStorage: React.createContext(),
  Player: React.createContext(),
  PlayerState: React.createContext(),
  PlayerViewMode: React.createContext(),
  PlayerProgress: React.createContext(),
  PlayerVolume: React.createContext(),
  API: React.createContext(),
  UI: React.createContext(),
}

const StoreProvider = ({store, options = {}, children = ''}) => {
  const [state, setState] = useState(() => {
    store.addChangedListener(store => setState(store.getState()))
    return store.getState()
  })
  const dispatch = store.dispatch
  const dispatchSessionEvent = useDispatchSessionEvent()

  useEffect(() => {
    const handleStart = event => {
      dispatchSessionEvent('adBreakStarted')
      store.dispatch(
        uiAction.adBreakStarted(
          event.getStreamData().adProgressData,
          event.getAd()?.getSkipTimeOffset()
        )
      )
    }

    return linkPluginEvents(options.plugins, {
      cuepointsChanged: (event, plugin) =>
        store.dispatch(
          uiAction.streamEventsChanged(
            event.getStreamData().cuepoints,
            plugin.getPlaybackStatus()
          )
        ),
      adBreakStarted: handleStart,
      adProgress: handleStart,
      adBreakEnded: () => {
        dispatchSessionEvent('adBreakEnded')
        store.dispatch(uiAction.adBreakEnded())
      },
    })
  }, [])

  return (
    <React.Fragment>
      <Context.Adapter.Provider value={store}>
        <Context.Full.Provider value={useCache({state, dispatch})}>
          <Context.Module.Provider
            value={useCache({state: state.Module, options, dispatch})}
          >
            <Context.UI.Provider value={useCache({state: state.UI, dispatch})}>
              <Context.API.Provider
                value={useCache({state: state.API, dispatch})}
              >
                <Context.Player.Provider
                  value={useCache({state: state.Player, dispatch})}
                >
                  <Context.PlayerState.Provider
                    value={useCache({
                      state: state.Player.playerState,
                      dispatch,
                    })}
                  >
                    <Context.PlayerViewMode.Provider
                      value={useCache({state: state.Player.viewMode, dispatch})}
                    >
                      <Context.PlayerProgress.Provider
                        value={useCache({
                          state: state.Player.progress,
                          dispatch,
                        })}
                      >
                        <Context.PlayerVolume.Provider
                          value={useCache({
                            state: state.Player.volume,
                            dispatch,
                          })}
                        >
                          {children}
                        </Context.PlayerVolume.Provider>
                      </Context.PlayerProgress.Provider>
                    </Context.PlayerViewMode.Provider>
                  </Context.PlayerState.Provider>
                </Context.Player.Provider>
              </Context.API.Provider>
            </Context.UI.Provider>
          </Context.Module.Provider>
        </Context.Full.Provider>
      </Context.Adapter.Provider>
    </React.Fragment>
  )
}

StoreProvider.propTypes = {
  store: PropTypes.object,
  options: PropTypes.object,
  plugins: PropTypes.array,
  children: PropTypes.node,
}

export default StoreProvider
