import {useState, useEffect, useRef, cloneElement} from 'react'
import {createPortal} from 'react-dom'
import PropTypes from 'prop-types'

import {getPopoverPosition} from 'util/index'
import {isDesktop} from 'util/environment'

const style = {
  zIndex: 7,
  position: 'fixed',
  padding: '8px 12px',
  borderRadius: 4,
  textAlign: 'center',
  color: 'white',
  backgroundColor: 'rgba(51, 51, 51, 0.625)',
}

const isOverflowing = element => element.scrollWidth > element.clientWidth

const Tooltip = ({
  title,
  bottom = '0px',
  overflowOnly,
  children,
  container = document.body,
}) => {
  const tooltipRef = useRef()
  const boxes = useRef()
  const [open, setOpen] = useState(false)
  const [position, setPosition] = useState(() => ({left: '100%'}))

  const childProps = {
    onMouseEnter: event => {
      if (!overflowOnly || isOverflowing(event.currentTarget)) {
        boxes.current = [
          event.currentTarget.getBoundingClientRect(),
          document.body.getBoundingClientRect(),
        ]
        setOpen(true)
      }
    },
    onMouseLeave: () => {
      setPosition({left: '100%'})
      setOpen(false)
    },
  }

  useEffect(() => {
    if (open) {
      const targetPosition = getPopoverPosition(
        tooltipRef.current.getBoundingClientRect(),
        ...boxes.current
      )
      targetPosition.left !== position.left && setPosition(targetPosition)
    }
  }, [open, position.left])

  return !isDesktop() && window.matchMedia('(any-hover: none)').matches ? (
    children
  ) : (
    <>
      {cloneElement(children, childProps)}
      {open &&
        createPortal(
          <div
            style={{
              ...style,
              ...position,
              top: `calc(${position.top}px - ${bottom})`,
            }}
            ref={tooltipRef}
          >
            {title}
          </div>,
          container
        )}
    </>
  )
}

Tooltip.propTypes = {
  title: PropTypes.node,
  bottom: PropTypes.string,
  overflowOnly: PropTypes.bool,
  children: PropTypes.node,
  container: PropTypes.object,
}

export default Tooltip
