import PropTypes from 'prop-types'
import Types from 'Types'
import I18n from 'context/I18n'
import {CastProvider} from './context'
import MiniControl from './container/MiniControl'

const CastSender = ({lang, langCustomCode, children, ...other}) => (
  <CastProvider lang={lang} {...other}>
    <I18n.Provider lang={lang} langCustomCode={langCustomCode}>
      {children}
      <MiniControl />
    </I18n.Provider>
  </CastProvider>
)

CastSender.propTypes = {
  appId: PropTypes.string,
  host: PropTypes.string,
  accessToken: PropTypes.string,
  deviceId: PropTypes.string,
  lang: Types.LanguageCode,
  langCustomCode: PropTypes.object,
  customHeaders: PropTypes.object,
  onConnected: PropTypes.func,
  onCasting: PropTypes.func,
  onError: PropTypes.func,
  children: PropTypes.node,
}

export default CastSender
