import PropTypes from 'prop-types'
import lang from 'config/lang'
import {
  LanguageCode as LanguageCodeEnum,
  PlayerState as PlayerStateEnum,
  AgentCommand as AgentCommandEnum,
  ItemType as ItemTypeEnum,
} from './Enum'

const ID = PropTypes.oneOfType([PropTypes.number, PropTypes.string])
const LanguageCode = PropTypes.oneOf(Object.values(LanguageCodeEnum))
const SupportEnvironmentItem = PropTypes.shape({
  device: PropTypes.shape({type: PropTypes.string}),
  os: PropTypes.shape({name: PropTypes.string, version: PropTypes.string}),
  browser: PropTypes.shape({name: PropTypes.string, version: PropTypes.string}),
})
const ItemType = PropTypes.oneOf(Object.values(ItemTypeEnum))
const VideoInfo = PropTypes.shape({
  contentId: ID,
  contentType: ItemType,
  licenseId: ID,
})
const PlayerState = PropTypes.oneOf(Object.values(PlayerStateEnum))
const TextCode = PropTypes.oneOf(
  Array.from(
    new Set(
      Object.entries(lang).reduce(
        (res, langItem) => res.concat(Object.keys(langItem[1])),
        []
      )
    )
  )
)
const AgentCommand = PropTypes.oneOf(Object.values(AgentCommandEnum))

export default {
  ID,
  LanguageCode,
  SupportEnvironmentItem,
  VideoInfo,
  PlayerState,
  TextCode,
  AgentCommand,
  ItemType,
}
