const getTargetQuality = (items, suggested) => {
  const sorted = items.sort((a, b) => b - a)
  if (!suggested) {
    return sorted[0]
  }
  return sorted.find(value => value <= suggested) || sorted.slice(-1)[0]
}

const filterHlsManifestQualities = (data, {sourceUrl, maxHeight}) => {
  if (RegExp(`RESOLUTION=\\d+x${maxHeight}\\W`).test(data)) {
    return data
      .replace(
        /(#EXT-X-STREAM-INF:.*RESOLUTION=\d+x)(\d+)(.*\n)(.*)(\n)/g,
        (match, part1, height, part2, fileName, line) =>
          height <= maxHeight
            ? [part1, height, part2, new URL(fileName, sourceUrl), line].join(
                ''
              )
            : ''
      )
      .replace(
        /(#EXT-X-MEDIA:.*URI=")(.*)(")/g,
        (match, part, fileName, line) =>
          [part, new URL(fileName, sourceUrl), line].join('')
      )
  }
}

const matchAll = (input, pattern) => {
  const flags = [
    pattern.global && 'g',
    pattern.ignoreCase && 'i',
    pattern.multiline && 'm',
  ]
    .filter(Boolean)
    .join('')
  const clone = new RegExp(pattern, flags)

  return Array.from(
    (function* () {
      let matched = true
      while (1) {
        matched = clone.exec(input)
        if (!matched) {
          return
        }
        yield matched
      }
    })()
  )
}

const rewriteHlsUrls = (manifest, sourceUrl) =>
  manifest.replace(
    /((#EXT-X-MEDIA:.*URI=")([^"]*))|((#EXT-X-STREAM-INF.*\n)(.*)(?=\n))/g,
    (...matches) =>
      [matches[2], matches[5], new URL(matches[3] || matches[6], sourceUrl)]
        .filter(Boolean)
        .join('')
  )

const rewriteHlsQualities = (manifest, {sourceUrl, filter}) => {
  if (!filter) {
    return
  }
  const files = matchAll(
    manifest,
    /RESOLUTION=(\d+)x(\d+)/g
  ).map(([, width, height]) => ({width: +width, height: +height}))
  const profiles = filter(files) || files
  if (profiles.length === 0) {
    return
  }
  const filtered = manifest.replace(
    /#EXT-X-STREAM-INF.*RESOLUTION=(\d+)x(\d+).*\n.*\n/g,
    (item, width, height) =>
      profiles.some(p => p.width === +width && p.height === +height) ? item : ''
  )
  if (filtered === manifest) {
    return
  }
  return rewriteHlsUrls(filtered, sourceUrl)
}

const compareQuality = (a, b) => a.height - b.height

const filterHigherQualites = ({qualities, targetQuality}) => {
  if (qualities.some(quality => compareQuality(quality, targetQuality) <= 0)) {
    return qualities.filter(
      quality => compareQuality(quality, targetQuality) <= 0
    )
  }
  return [qualities.reduce((a, b) => (compareQuality(a, b) <= 0 ? a : b))]
}

export {getTargetQuality, filterHlsManifestQualities}
export {rewriteHlsQualities, filterHigherQualites}
