/* eslint-disable indent */
/* @jsxImportSource @emotion/react */
import {useState, useRef} from 'react'
import PropTypes from 'prop-types'

import {havePointerQuery} from 'util/environment'

const style = {
  position: 'relative',
  height: '100%',
  display: 'flex',
  alignItems: 'center',
  cursor: 'pointer',
  userSelect: 'none',
  touchAction: 'none',
}

const railStyle = {
  position: 'relative',
  flex: '100%',
  height: '4px',
  overflow: 'hidden',
  background: 'rgba(255, 255, 255, 0.3)',
  '> div': {
    position: 'absolute',
    top: '0',
    left: '0',
    width: '100%',
    height: '100%',
  },
}

const markStyle = {
  position: 'absolute',
  height: railStyle.height,
  width: '4px',
  transform: 'translateX(-50%)',
  backgroundColor: '#ff9835',
}

const thumbStyle = {
  position: 'absolute',
  top: '50%',
  height: '14px',
  width: '14px',
  borderRadius: '100%',
  backgroundColor: '#fff',
  boxShadow: '0 2px 2px 0 rgba(0, 0, 0, 0.5)',
  transform: 'translate(-50%, -50%)',
}

const getPointerData = event => {
  const {pageX: x, pageY: y} =
    event.touches?.[0] || event.changedTouches?.[0] || event
  const {width, left} = event.currentTarget.getBoundingClientRect()
  return {x, y, width, left}
}

const getSliderValue = ({x, left, width}) =>
  Math.max(0, Math.min((x - left) / width, 1))

const debouncedPointerHandlers = ({state, onMove, onLeave}) => {
  const emit = () => {
    if (!state.scheduled) {
      return
    }
    if (state.type === 'leave') {
      onLeave?.(state.event, state)
    } else {
      onMove(state.event, state)
    }
    state.scheduled = false
  }
  const schedule = () => {
    if (state.scheduled) {
      return
    }
    state.scheduled = true
    requestAnimationFrame(emit)
  }

  return {
    onPointerMove: event => {
      const type =
        event.buttons > 0 || event.touches?.length > 0 ? 'change' : 'move'
      Object.assign(state, {event, type, ...getPointerData(event)})
      schedule()
    },
    onPointerLeave: event => {
      const type = 'leave'
      Object.assign(state, {event, type})
      schedule()
    },
    emit,
  }
}

const eventHandlers = ({
  onPointerDown,
  onPointerMove,
  onPointerLeave,
  onPointerUp,
}) =>
  window.matchMedia(havePointerQuery).matches
    ? {onPointerDown, onPointerMove, onPointerLeave, onPointerUp}
    : {
        onTouchStart: onPointerDown,
        onTouchMove: onPointerMove,
        onTouchEnd: event => {
          onPointerLeave(event)
          onPointerUp(event)
        },
      }

const SimpleSlider = ({
  min = 0,
  max = 100,
  value,
  marks = [],
  className = '',
  classes = {},
  onPointerMove,
  onPointerLeave,
  onChange,
  onChangeCommitted,
}) => {
  const pointerState = useRef({})
  const [focusValue, setFocusValue] = useState(-1)
  const currentPosition =
    ((focusValue >= 0 ? focusValue : value) - min) / (max - min)
  const pointerHandlers = debouncedPointerHandlers({
    state: pointerState.current,
    onMove: (event, {type, x, y, width, left}) => {
      const pointerValue = min + max * getSliderValue({x, width, left})
      onPointerMove?.(event, {value: pointerValue, x, y})
      if (type === 'change') {
        setFocusValue(pointerValue)
        onChange?.(event, {value: pointerValue, x, y})
      }
    },
    onLeave: () => onPointerLeave?.(),
  })
  const handlePointerUp = event => {
    if (event.pointerId) {
      event.currentTarget.releasePointerCapture(event.pointerId)
    }
    setFocusValue()
    const pointerValue = min + max * getSliderValue(getPointerData(event))
    pointerHandlers.emit()
    onChangeCommitted?.(event, {value: pointerValue})
  }

  return (
    <div
      className={`kks-player__slider ${className}`}
      css={style}
      onClick={event => event.stopPropagation()}
      {...eventHandlers({
        onPointerDown: event => {
          if (event.type === 'pointerdown') {
            event.currentTarget.setPointerCapture(event.pointerId)
          }
          pointerHandlers.onPointerMove(event)
        },
        ...pointerHandlers,
        onPointerUp: handlePointerUp,
      })}
    >
      <div
        className={`kks-player__slider-rail ${classes.rail || ''}`}
        css={railStyle}
      >
        <div
          css={{backgroundColor: '#fff'}}
          className={`kks-player__slider-track ${classes.track || ''}`}
          style={{transform: `translateX(${currentPosition * 100 - 100}%)`}}
        />
      </div>
      {marks.map(position => (
        <div
          key={position}
          css={markStyle}
          className={classes.marked}
          style={{left: `${(position / max) * 100}%`}}
        />
      ))}
      <div
        css={thumbStyle}
        className={`kks-player__slider-thumb ${classes.thumb}`}
        style={{left: `calc(${currentPosition * 100}%)`}}
      />
    </div>
  )
}

SimpleSlider.propTypes = {
  min: PropTypes.number,
  max: PropTypes.number,
  value: PropTypes.number,
  marks: PropTypes.array,
  className: PropTypes.string,
  classes: PropTypes.object,
  onPointerMove: PropTypes.func,
  onPointerLeave: PropTypes.func,
  onChange: PropTypes.func,
  onChangeCommitted: PropTypes.func,
}

export default SimpleSlider
