import {useState, useEffect, useRef, useMemo} from 'react'
import shallowCompare from 'zustand/shallow'

export const useTimeout = () => {
  const timer = useRef({})
  useEffect(() => () => clearTimeout(timer.current.id), [])
  return [
    (callback, ms) => {
      clearTimeout(timer.current.id)
      timer.current.id = setTimeout(callback, ms)
    },
    () => clearTimeout(timer.current.id),
  ]
}

export const useInterval = () => {
  const timer = useRef({})
  useEffect(() => () => clearInterval(timer.current.id), [])
  return [
    (callback, ms) => {
      clearInterval(timer.current.id)
      timer.current.id = setInterval(callback, ms)
    },
    () => clearInterval(timer.current.id),
  ]
}

export const useCache = newState => {
  const [state, setState] = useState()
  useEffect(() => {
    if (!shallowCompare(state, newState)) setState(newState)
  })
  return shallowCompare(state, newState) ? state : newState
}

export const useMemoizedRef = init => {
  const value = useMemo(init, [])
  return useRef(value)
}

const useDebounce = ({value, delay}) => {
  const [state, dispatch] = useState(value)
  const waiting = useRef()

  useEffect(() => {
    if (waiting.current) {
      waiting.current.value = value
    } else {
      waiting.current = {}
      setTimeout(() => {
        if ('value' in waiting.current) {
          dispatch(waiting.current.value)
        }
        waiting.current = undefined
      }, delay)
      dispatch(value)
    }
  }, [value])

  return state
}

const useAutoHide = ({hideTimeMs = 3000, pinned, tapToHide, onHide} = {}) => {
  const timer = useRef()
  const [mode, setMode] = useState('hidden')

  const interact = () => {
    if (mode !== 'shown') {
      setMode('shown')
    }
    clearTimeout(timer.current)
    timer.current = setTimeout(() => setMode('hidden'), hideTimeMs)
  }
  const hide = () => {
    clearTimeout(timer.current)
    setMode('hidden')
    onHide?.()
  }

  useEffect(() => {
    if (mode === 'shown') {
      interact()
    }
  }, [hideTimeMs])

  useEffect(() => {
    if (pinned) {
      clearTimeout(timer.current)
    } else {
      interact()
    }
  }, [pinned])

  useEffect(() => () => clearTimeout(timer.current), [])

  return {
    mode: pinned ? 'shown' : mode,
    show: interact,
    hide,
    onClick: event => {
      if (mode === 'hidden') {
        interact()
      } else if (tapToHide && event.target.tagName !== 'BUTTON') {
        // hide if tapping on elsewhere
        hide()
      }
    },
    onMouseMove: () => {
      // In mobile web, emulated clicks generate extra mouse move events
      if (!('ontouchstart' in window)) {
        interact()
      }
    },
  }
}

export {useAutoHide, useDebounce}
