import getErrorInfo from 'playerCore/getErrorInfo'

export const type = {
  INITIALIZE: 'INITIALIZE',
  INITIALIZED: 'INITIALIZED',
  LOAD_CONTENT: 'LOAD_CONTENT',
  READY_TO_PLAY: 'READY_TO_PLAY',
  BACK: 'BACK',
  CAST_STATE_CHAGNE: 'CAST_STATE_CHAGNE',
  REPLAY: 'REPLAY',

  ERROR: 'ERROR',
}
export default {
  initialize: () => ({type: type.INITIALIZE}),
  initialized: () => ({type: type.INITIALIZED}),
  loadContent: () => ({type: type.LOAD_CONTENT}),
  readyToPlay: () => ({type: type.READY_TO_PLAY}),
  back: () => ({type: type.BACK}),
  castStateChange: castState => ({type: type.CAST_STATE_CHAGNE, castState}),
  replay: () => ({type: type.REPLAY}),

  error: (from, error) => {
    console.error(error)
    return {type: type.ERROR, from, error: getErrorInfo(error || undefined)}
  },
}
