/* @jsxImportSource @emotion/react */
import {useContext} from 'react'
import PropTypes from 'prop-types'

import {ModuleState} from 'Enum'
import {Context} from 'player/store'

const imageStyle = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  width: '100%',
  height: '100%',
  objectFit: 'contain',
  transform: 'translate(-50%, -50%)',
}

const CoverImageWrap = ({url, children}) => {
  const {
    state: {state: moduleState},
  } = useContext(Context.Module)

  return url &&
    [
      ModuleState.UNINITIALIZATION,
      ModuleState.INITIALIZING,
      ModuleState.INITIALIZED,
    ].includes(moduleState) ? (
    <img css={imageStyle} src={url} />
  ) : (
    children
  )
}

CoverImageWrap.propTypes = {
  url: PropTypes.string,
  children: PropTypes.node,
}

export default CoverImageWrap
