/* @jsxImportSource @emotion/react */
import {useState, useEffect, useContext, useRef, Fragment} from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import useOnclickOutside from 'react-cool-onclickoutside'

import {useChangeSettings, useSettings} from 'player/settings'
import {Context} from 'player/store'
import Action from 'player/store/action/UI'
import icon from 'style/icon'
import {FormattedMessage} from 'context/I18n'

const style = {
  head: {
    position: 'sticky',
    zIndex: '1',
    top: '0',
    display: 'flex',
    alignItems: 'center',
    padding: '1rem 1.5rem',
    color: 'white',
    backgroundColor: 'inherit',
    fontSize: '16px',
    fontWeight: 'bold',
    button: {
      marginRight: '1rem',
      padding: '0',
      width: '1rem',
      height: '1rem',
      border: 'none',
    },
  },
  overlay: {
    position: 'absolute',
    zIndex: '-2',
    top: '0',
    width: '100%',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'rgba(0, 0, 0, 0.6)',
    opacity: '0',
    transition: 'opacity 0.2s ease, transform 0s ease 0.2s',
  },
  container: {
    flex: '0 18rem',
    maxHeight: 'calc(100% - 2rem)',
    color: '#ccc',
    background: '#333333',
    whiteSpace: 'nowrap',
    borderRadius: '4px',
    userSelect: 'none',
    overflow: 'auto',
  },
  opening: {
    zIndex: '0',
    opacity: '1',
    transform: 'translateY(0)',
    transition: 'opacity 0.2s ease',
  },
  title: {
    padding: '12px 18px',
  },
  dismiss: {
    background: `center / 1rem no-repeat url(${icon.close}), transparent`,
  },
  back: {
    background: `center / 1rem no-repeat url(${icon.back}), transparent`,
  },
  row: {
    cursor: 'pointer',
    display: 'flex',
    padding: '1rem 1.5rem',
    fontSize: '16px',
    '::after': {
      content: '" "',
      marginLeft: '1rem',
      width: '20px',
      height: '20px',
      display: 'inline-block',
      color: 'white',
      backgroundPosition: 'center',
      backgroundSize: 'cover',
    },
  },
  space: {
    flex: '1',
  },
  hasOptions: {
    '::after': {
      backgroundImage: `url(${icon.arrowTop})`,
      transform: 'rotate(90deg)',
    },
  },
  selected: {
    '::after': {
      backgroundImage: `url(${icon.check})`,
    },
  },
}

const dekstopStyle = {
  overlay: {
    position: 'absolute',
    bottom: '5em',
    right: '3rem',
    display: 'flex',
    alignItems: 'flex-end',
    width: '15rem',
    height: 'calc(100% - 8rem)',
    opacity: '0',
    transform: 'translateY(-100vh)',
  },
  container: {
    ...style.container,
    maxHeight: '100%',
    background: 'rgba(0, 0, 0, 0.7)',
  },
  head: {
    ...style.head,
    background: '#000',
  },
  row: {
    ...style.row,
    '::after': {},
    '::before': {
      ...style.row['::after'],
      marginLeft: '0',
      marginRight: '4px',
    },
  },
  hasOptions: {
    '::before': {
      backgroundImage: `url(${icon.arrowTop})`,
      transform: 'rotate(90deg)',
    },
  },
  selected: {
    '::before': {
      backgroundImage: `url(${icon.check})`,
    },
  },
}

const MenuItemText = ({text}) => (
  <FormattedMessage id={`KKS.SETTING.${text}`} getDefault={() => text} />
)

MenuItemText.propTypes = {
  text: PropTypes.string,
}

const MenuSection = ({sharedStyle, items = [], selectedValue, onSelect}) => (
  <div className="kks-player__menu-content">
    {items.map(({value, label}) => (
      <div
        key={value}
        className={classnames('kks-player__menu-item', {
          'kks-player__menu-item--selected': selectedValue === value,
        })}
        css={[sharedStyle.row, selectedValue === value && sharedStyle.selected]}
        onClick={() => onSelect(value)}
      >
        <MenuItemText text={label || value} />
        <div css={[style.space]} />
      </div>
    ))}
  </div>
)

MenuSection.propTypes = {
  sharedStyle: PropTypes.object,
  items: PropTypes.array,
  selectedValue: PropTypes.string,
  MenuItem: PropTypes.elementType,
  onSelect: PropTypes.func,
}

const PlayerSettings = ({desktop, hasBottomPanel}) => {
  const sharedStyle = desktop ? dekstopStyle : style
  const {
    state: {isSettingsOpen},
    dispatch,
  } = useContext(Context.UI)
  const [path, setPath] = useState('/')
  const menuSections = useSettings()
  const changeSettings = useChangeSettings()

  useEffect(() => {
    if (!isSettingsOpen) {
      setPath('/')
    }
  }, [isSettingsOpen])

  const ref = useRef()
  useOnclickOutside(
    () => {
      if (isSettingsOpen) {
        dispatch(Action.hideMenu())
      }
    },
    {
      refs: [ref],
      eventTypes: ['click'],
    }
  )

  return (
    <div
      css={[
        sharedStyle.overlay,
        isSettingsOpen && style.opening,
        hasBottomPanel && {
          bottom: '8em',
        },
      ]}
    >
      <div ref={ref} className="kks-player__menu" css={sharedStyle.container}>
        {path === '/' && (
          <Fragment>
            <div className="kks-player__menu-head" css={sharedStyle.head}>
              {!desktop && (
                <button
                  css={style.dismiss}
                  onClick={() => dispatch(Action.hideMenu())}
                />
              )}
              <FormattedMessage id="KKS.SETTING" />
            </div>
            {menuSections.map(({section, title, items, selectedValue}) => {
              const selectedName =
                items.find(item => item.value === selectedValue)?.label ||
                selectedValue

              return (
                <div
                  css={[style.row, style.hasOptions]}
                  key={section}
                  onClick={() => setTimeout(() => setPath(`/${section}`), 1)}
                >
                  <FormattedMessage id={title} />
                  <div css={[style.space]} />
                  <MenuItemText text={selectedName} />
                </div>
              )
            })}
          </Fragment>
        )}
        {menuSections.map(
          ({section, title, items, selectedValue}) =>
            path === `/${section}` && (
              <Fragment key={section}>
                <div className="kks-player__menu-head" css={sharedStyle.head}>
                  <button
                    css={style.back}
                    onClick={() => setTimeout(() => setPath('/'), 1)}
                  />
                  <FormattedMessage id={title} />
                </div>
                <MenuSection
                  key={section}
                  sharedStyle={sharedStyle}
                  {...{items, selectedValue}}
                  onSelect={value => {
                    dispatch(Action.hideMenu())
                    if (value !== selectedValue) {
                      changeSettings({section, value})
                    }
                  }}
                />
              </Fragment>
            )
        )}
      </div>
    </div>
  )
}

PlayerSettings.propTypes = {
  desktop: PropTypes.bool,
  hasBottomPanel: PropTypes.bool,
}

export default PlayerSettings
