/* @jsxImportSource @emotion/react */
/* eslint-disable react/prop-types */
const expand = {
  margin: 0,
  flex: '1',
}

const containerStyle = {
  position: 'absolute',
  top: 0,
  left: 0,
  width: '100%',
  height: '100%',
  boxSizing: 'border-box',
  display: 'flex',
  flexDirection: 'column',
  overflow: 'hidden',
  color: 'white',
  // prevent animation glich(afterimage) of descendant elements
  transform: 'translateX(0)',
  userSelect: 'none',
  h1: {
    margin: 0,
    fontSize: '1em',
    lineHeight: '1.5em',
  },
  'a, a:link, a:visited': {
    color: '#fff',
    opacity: 0.8,
    textDecoration: 'none',
  },
  button: {
    fontSize: 'inherit',
  },
}

const drop = {
  backgroundImage: `linear-gradient(
    0deg,
    rgba(0,0,0,0.5) 0,
    rgba(0,0,0,0) 8rem calc(100% - 8rem),
    rgba(0,0,0,0.5) 100%
  )`,
}

const dropTop = {
  backgroundImage: `linear-gradient(
    0deg,
    rgba(0,0,0,0) 0,
    rgba(0,0,0,0) 8rem calc(100% - 8rem),
    rgba(0,0,0,0.5) 100%
  )`,
}

const responsiveStyles = {
  desktop: {
    fontSize: '24px',
  }, // add if necessary: big-desktop
}

const rowStyle = {
  boxSizing: 'border-box',
  width: '100%',
  padding: 'calc(2em - 16px)',
  display: 'flex',
  alignItems: 'center',
  button: {
    flex: '0 0 1.5em',
    width: '1.5em',
    height: '1.5em',
  },
  '> button:not(:disabled) ~ button:not(:disabled), div ~ button:last-of-type': {
    marginLeft: '0.5rem',
  },
  '> button:not(:last-of-type)': {
    marginRight: '0.5rem',
  },
}

const displayStyles = {
  hidden: {
    '> div:not(.pinned), > button:not(.pinned), > h1:not(.pinned)': {
      opacity: 0,
      transform: 'translate(-100vw)',
      transition: 'opacity 0.5s ease-out, transform 0s 0.5s',
    },
  },
  shown: {
    '> div:not(.pinned), > button:not(.pinned), > h1:not(.pinned)': {
      transition: 'opacity 0.5s ease-out',
    },
  },
}

const controlsDisplayStyles = {
  hidden: {
    ...displayStyles.hidden,
    '~ div:not(.pinned)': displayStyles.hidden,
  },
  shown: {
    ...displayStyles.display,
    '~ div': displayStyles.display,
  },
}

const controlsStyle = {
  position: 'absolute',
  zIndex: '2',
  display: 'flex',
  alignItems: 'center',
  '> button': {
    margin: '1em',
    width: '1.75em',
    height: '1.75em',
    '&:disabled': {
      opacity: 0.3,
    },
    '&.play-button': {
      width: '3em',
      height: '3em',
    },
  },
}

const textEllipsis = {
  overflow: 'hidden',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
}

const infoStyle = {
  alignItems: 'flex-start',
  overflow: 'visible',
  h1: {
    height: '3em',
    fontWeight: '500',
    ...textEllipsis,
    '> div': textEllipsis,
  },
}

const backStyle = {
  position: 'absolute',
  zIndex: '-1',
  width: '100%',
  height: '100%',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}

const skipStyle = {
  position: 'absolute',
  right: 0,
  bottom: '9rem',
  textAlign: 'right',
  button: {
    width: 'auto',
    height: 'auto',
  },
}

const dekstopStyle = {
  '> div': {
    '> button:not(:disabled) ~ button:not(:disabled), button:last-of-type': {
      marginLeft: '0.75em',
    },
    '> button:not(:last-of-type), > button:first-of-type, > button ~ div': {
      marginRight: '0.75em',
    },
    '> button:first-of-type': {
      marginLeft: '0',
    },
  },
}

const desktopControls = {
  flexWrap: 'wrap',
  '> div:first-of-type': {
    flex: '100%',
    marginBottom: '0.88em',
  },
  'button[disabled]': {
    display: 'none',
  },
}

const ControlsBlock = ({
  order = 'mobile',
  playButton,
  rewindButton = '',
  forwardButton = '',
  previousEpisodeButton = '',
  nextEpisodeButton = '',
}) =>
  order === 'desktop' ? (
    <>
      {previousEpisodeButton}
      {playButton}
      {nextEpisodeButton}
      {rewindButton}
      {forwardButton}
    </>
  ) : (
    <>
      {rewindButton}
      {previousEpisodeButton}
      {playButton}
      {nextEpisodeButton}
      {forwardButton}
    </>
  )

const DefaultLayout = ({
  type = 'mobile',
  display,
  controlsDisplay,
  size,
  title = '',
  channelTitle = '',
  haveBottomItem,
  seekbar = '',
  controlButtons,
  volumeControl,
  functionButtons,
  fullscreenButton,
  backButton,
  adStatus = '',
  adLink = '',
  adSkipButton,
  backItems,
  children,
  containerRef,
  backRef,
  ...rest
}) => (
  <div
    css={[
      containerStyle,
      responsiveStyles[size],
      type === 'desktop' && dekstopStyle,
      display === 'shown' && (haveBottomItem ? dropTop : drop),
    ]}
    ref={containerRef}
    {...rest}
  >
    <div ref={backRef} css={backStyle}>
      {type === 'mobile' ? (
        <div css={[controlsStyle, displayStyles[controlsDisplay]]}>
          <ControlsBlock order="mobile" {...controlButtons} />
        </div>
      ) : (
        backItems
      )}
      {adSkipButton && <div css={skipStyle}>{adSkipButton}</div>}
    </div>
    <div css={[rowStyle, infoStyle, displayStyles[display]]}>
      {backButton}
      <h1>
        {title}
        {channelTitle && <div css={{fontSize: '16px'}}>{channelTitle}</div>}
      </h1>
      <div css={expand} />
      {type === 'mobile' && functionButtons}
      {adLink && <div className="pinned">{adLink}</div>}
    </div>
    <div
      css={[
        rowStyle,
        {marginTop: 'auto'},
        type === 'desktop' && desktopControls,
        controlsDisplayStyles[controlsDisplay],
      ]}
    >
      {seekbar || <div />}
      {type === 'desktop' && (
        <ControlsBlock order="desktop" {...controlButtons} />
      )}
      {adStatus && (
        <div className="pinned" css={{flex: 1, textShadow: '2px 2px 1px #000'}}>
          {adStatus}
        </div>
      )}
      {type === 'desktop' && (
        <>
          <div css={expand} />
          {volumeControl}
          {functionButtons}
        </>
      )}
      {fullscreenButton}
    </div>
    {children}
  </div>
)

export default DefaultLayout
